import { useEffect, useState } from "react";
import EmailBodyModal from "./EmailBodyModal";
import GenWithAiModal from "@/components/GenWithAiModal";
import { axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { motion } from "framer-motion";
import { produce } from "immer";

export default function TablePreview({
  leads,
  setExpandTable,
  expandTable,
  setLeads,
  onSave,
  onCancel,
}) {
  const tableHeaders: any[] = Object.keys(leads[0] ?? {});
  const allRowsSelected = leads.every((lead) => lead.selected);
  const [messageType, setMessageType] = useState("email");
  const [editEmailTable, setEditEmailTable] = useState(false);
  const [showGenWithAiModal, setShowGenWithAiModal] = useState(false);
  const [generatedData, setGeneratedData] = useState([]);
  const [body, setBody] = useState("");
  const [subject, setSubject] = useState("");
  const [buttonLoading, setButtonLoading] = useState(false);
  const dispatch = useDispatch();

  function generateContent(body: string, subject: string) {
    const content = `I would like to update the email content based on the following information:
    subject: ${JSON.stringify(subject)}
    body: ${JSON.stringify(body)}
    Please enhance the professionalism and engagement of the email. The recipient's name remains the same. The return body and subject should be in JSON format.`;

    const emailBody = {
      configId: "6629fd49faa87bdbb7f9ded5",
      content: content,
    };

    axiosPost(`/configurations/completechat?responses=${2}`, emailBody)
      .then((response) => {
        if (response.status === 200) {
          setEditEmailTable(false);
          setShowGenWithAiModal(true);
          let tempArr: any = [];
          response.data.choices.forEach((data, index) => {
            tempArr.push(JSON.parse(data.message?.content));
          });
        }
      })
      .catch((err) => {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message:
              err.response?.data?.message ??
              err.message ??
              "Something went wrong, please try again.",
          }),
        );
      });
  }

  const saveSelectedLeads = () => {
    setLeads((prev) => {
      return prev.filter((lead) => lead.selected);
    });
    setExpandTable(false);
    onSave();
  };

  const toggleSelectionOnLead = (index) => {
    setLeads((prev) =>
      produce(prev, (draft) => {
        draft[index].selected = !draft[index].selected;
      }),
    );
  };

  return (
    <div className="h-100 gen-email-modal">
      <div className="all-template-body fx fx-d-col pd-20 gap-15">
        <div className="ob-table-head fx jstfy-sp-btw al-fx-cnt">
          <h3>
            {leads.length} Result{leads.length > 1 ? "s" : ""}
          </h3>
          <div
            className="maxmin-btn br-5 fx al-cnt cursor-pointer gap-8"
            onClick={() => setExpandTable(!expandTable)}
          >
            {expandTable ? <MinIcon /> : <MaxIcon />}
            {expandTable ? "Minimize" : "Maximize"} Table
          </div>
        </div>
        <table className="ob-table no-mt br-5 fx fx-d-col new-scrollbar">
          <thead className="fx p-sticky top-0">
            <tr
              style={{
                gridTemplateColumns: `58px repeat(${tableHeaders?.filter((header) => header !== "selected").length}, minmax(198px, 1fr))`,
              }}
            >
              <th>
                <label className="checkbox-container link-mark no-m">
                  <input
                    className="bot-check"
                    type="checkbox"
                    onClick={() => {
                      if (allRowsSelected) {
                        setLeads((prev) => {
                          return prev.map((lead, index) => {
                            return { ...lead, selected: false };
                          });
                        });
                      } else {
                        // setSelectedRows([...Array(tableLeads.length).keys()]);
                        setLeads((prev) => {
                          return prev.map((lead, index) => {
                            return { ...lead, selected: true };
                          });
                        });
                      }
                    }}
                    checked={allRowsSelected}
                  />
                  <span className="mark">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="16"
                      height="16"
                      viewBox="0 0 16 16"
                      fill="none"
                    >
                      <rect width="16" height="16" rx="5" />
                      <path
                        d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                        fill="white"
                      />
                    </svg>
                  </span>
                </label>
              </th>
              {tableHeaders
                .filter((header) => header !== "selected")
                .map((header, index) => (
                  <th
                    key={index}
                    className={`${header === "body" ? "w-507" : " "}`}
                  >
                    {header}
                  </th>
                ))}
            </tr>
          </thead>
          <tbody className="fx fx-d-col">
            {leads?.length ? (
              leads.map((lead, leadIndex: number) => (
                <tr
                  key={leadIndex}
                  style={{
                    gridTemplateColumns: `58px repeat(${tableHeaders.filter((header) => header !== "selected")?.length}, minmax(198px, 1fr))`,
                  }}
                >
                  <td>
                    <label className="checkbox-container link-mark no-m">
                      <input
                        className="bot-check"
                        type="checkbox"
                        checked={lead.selected}
                        onClick={() => {
                          toggleSelectionOnLead(leadIndex);
                        }}
                      />
                      <span className="mark">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 16 16"
                          fill="none"
                        >
                          <rect width="16" height="16" rx="5" />
                          <path
                            d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                            fill="white"
                          />
                        </svg>
                      </span>
                    </label>
                  </td>
                  {tableHeaders
                    .filter((header) => header !== "selected")
                    .map((header, headerIndex) => (
                      <td
                        key={headerIndex}
                        className={`${header === "body" ? "email_body_content" : ""} `}
                      >
                        {header === "body" ? (
                          <>
                            <textarea
                              value={lead[header]}
                              onChange={(e) => {
                                leads((prev: any) => {
                                  const newTableLeads = [...prev];
                                  newTableLeads[leadIndex][header] =
                                    e.target.value;
                                  return newTableLeads;
                                });
                              }}
                            ></textarea>
                            <span onClick={() => setEditEmailTable(true)}>
                              <EditEmailIconSvg />
                            </span>
                          </>
                        ) : (
                          <input
                            type="text"
                            value={lead[header]}
                            onChange={(e) => {
                              leads((prev: any) => {
                                const newTableLeads = [...prev];
                                newTableLeads[leadIndex][header] =
                                  e.target.value;
                                return newTableLeads;
                              });
                            }}
                          />
                        )}
                      </td>
                    ))}
                </tr>
              ))
            ) : (
              <div className="no_data_found">
                <NoDataFound />
                <h1>No data found</h1>
              </div>
            )}
          </tbody>
        </table>
      </div>
      <div className="call-api-footer fx fx-d-row">
        {buttonLoading ? (
          <div className="load-container">
            <div className="linespinner"></div>
          </div>
        ) : (
          <>
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.85 }}
              className="save-btn fw-400 f-14"
              onClick={saveSelectedLeads}
              disabled={leads.every((lead) => !lead.selected)}
            >
              Continue
            </motion.button>
            <motion.button
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.85 }}
              className="cancel_btn fw-400 f-14 cncl-leads"
              onClick={() => {
                setExpandTable(false);
                onCancel();
              }}
            >
              Cancel
            </motion.button>
          </>
        )}
      </div>
      {editEmailTable && (
        <EmailBodyModal
          setEditEmailTable={setEditEmailTable}
          body={body}
          setBody={setBody}
          subject={subject}
          onSave={(subject, body, editorContent) => {
            setBody(body);
            setSubject(subject);
          }}
          setSubject={setSubject}
          setShowGenWithAiModal={setShowGenWithAiModal}
          generateContent={generateContent}
          allData={leads}
          messageType={messageType}
        />
      )}
      {showGenWithAiModal && (
        <GenWithAiModal
          nodeName="sendEmail"
          setEditEmailTable={setEditEmailTable}
          setShowGenWithAiModal={setShowGenWithAiModal}
          generatedData={generatedData}
          onBoarding={true}
          onSave={(subject, body) => {
            setBody(body);
            setSubject(subject);
          }}
          onClose={() => {
            setShowGenWithAiModal(false);
          }}
        />
      )}
    </div>
  );
}

