import { useState, useMemo, useEffect } from "react";
import EmailBodyModal from "./EmailBodyModal";
import GenWithAiModal from "@/components/GenWithAiModal";
import { axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { Schedule } from "./types";
import { useQueryClient } from "@tanstack/react-query";
import { set } from "lodash";

type EditorContentMapping = {
  leadIndex: number;
  dayIndex: number;
  emailIndex: number;
  content: string;
};

let editorContentMapping: EditorContentMapping[] = [];

const DaysTable = ({
  aiGeneratedLeads,
  setExpandTable,
  expandTable,
  onClose,
  generateFollowUpAIContent,
  AIContentStep,
  sendCampaign,
  editInBuilder,
  scheduleData,
  workflowLoader,
  disableGeneration = false,
}) => {
  const dispatch = useDispatch();
  const [editEmailTable, setEditEmailTable] = useState(false);
  const [showGenWithAiModal, setShowGenWithAiModal] = useState(false);
  const [body, setBody] = useState("");
  const [subject, setSubject] = useState("");
  const [messageType, setMessageType] = useState("email");
  const [generatedData, setGeneratedData] = useState<any[]>([]);
  const [leadIndex, setLeadIndex] = useState(0);
  const [dayIndex, setDayIndex] = useState("");
  const queryClient = useQueryClient();
  const formattedScheduleData = useMemo(() => {
    const modifiedScheduleData = [...scheduleData];
    if (AIContentStep === "initial") modifiedScheduleData.splice(3);
    const data = formatScheduleData(modifiedScheduleData);
    return data;
  }, [scheduleData, AIContentStep]);
  const [updatedEdittedContent, setUpdatedEdittedContent] = useState<any>();
  const [loader, setLoader] = useState(false);

  useEffect(() => {
    adjustEditIconPosition();
  }, [expandTable]);

  useEffect(() => {
    optimizedAdjustEditIconPosition();
    window.addEventListener("load", optimizedAdjustEditIconPosition);
    window.addEventListener("scroll", optimizedAdjustEditIconPosition, true);
    window.addEventListener("wheel", optimizedAdjustEditIconPosition, true);
    window.addEventListener(
      "pointermove",
      optimizedAdjustEditIconPosition,
      true,
    );
    window.addEventListener("resize", optimizedAdjustEditIconPosition);

    return () => {
      window.removeEventListener("load", optimizedAdjustEditIconPosition);
      window.removeEventListener(
        "scroll",
        optimizedAdjustEditIconPosition,
        true,
      );
      window.removeEventListener(
        "wheel",
        optimizedAdjustEditIconPosition,
        true,
      );
      window.removeEventListener(
        "pointermove",
        optimizedAdjustEditIconPosition,
        true,
      );
      window.removeEventListener("resize", optimizedAdjustEditIconPosition);
    };
  }, []);

  const adjustEditIconPosition = () => {
    const elements = document.querySelectorAll(".action_li_hover");
    elements.forEach((element) => {
      const rect = element.getBoundingClientRect();
      const viewportWidth =
        window.innerWidth || document.documentElement.clientWidth;
      const visibleWidth = Math.max(
        0,
        Math.min(rect.right, viewportWidth) - Math.max(rect.left, 0),
      );

      const editIconContainer = element.querySelector(
        ".edit_icon",
      ) as HTMLElement;

      if (editIconContainer) {
        const currentLeft = editIconContainer.style.left;
        let newLeft;

        if (visibleWidth < rect.width) {
          newLeft = `${visibleWidth - 70}px`;
        } else {
          newLeft = "";
        }

        if (currentLeft !== newLeft) {
          editIconContainer.style.left = newLeft;
        }
      }
    });
  };
  const optimizedAdjustEditIconPosition = () => {
    requestAnimationFrame(adjustEditIconPosition);
  };

  const ensureLinkedInActions = (leads, scheduleData) => {
    leads.forEach((lead) => {
      Object.keys(scheduleData).forEach((day) => {
        if (!lead[day]) {
          lead[day] = [];
        }
        const scheduledActionTypes = scheduleData[day].map(
          (action) => action.type,
        );
        if (
          scheduledActionTypes.includes("linkedinVisit") &&
          !lead[day].some((action) => action.type === "linkedinVisit")
        ) {
          lead[day].push({ type: "linkedinVisit" });
        }
        if (
          scheduledActionTypes.includes("linkedinConnect") &&
          !lead[day].some((action) => action.type === "linkedinConnect")
        ) {
          lead[day].push({ type: "linkedinConnect" });
        }
      });
    });
  };

  ensureLinkedInActions(aiGeneratedLeads, formattedScheduleData);

  function formatScheduleData(scheduleData: Array<Schedule>) {
    const formattedData: any = {};
    for (let sd of scheduleData) {
      const key = "day_" + sd.day;
      sd.nodes.map((node: string) => {
        if (!formattedData[key]) formattedData[key] = [];
        if (node === "email") {
          formattedData[key].push({ type: node, subject: "", body: "" });
        } else if (node === "linkedinMsg") {
          formattedData[key].push({ type: node, body: "" });
        } else formattedData[key].push({ type: node });
      });
    }

    return formattedData;
  }

  const stripHtml = (content) => {
    const tempDiv = document.createElement("div");
    tempDiv.innerHTML = content;
    return tempDiv.textContent || tempDiv.innerHTML || "";
  };

  const handleDayUpdate = (
    leadIndex: number,
    dayIndex: string,
    body: string,
    subject: string,
    editorContent: string,
  ) => {
    const dayEntries = aiGeneratedLeads[leadIndex][dayIndex];
    const strippedEditorContent = stripHtml(editorContent);

    const replacedBody = body.replaceAll("\n", "");

    // Find the index for email and LinkedIn message
    const emailIndex = dayEntries.findIndex((item) => item.type === "email");
    const linkedinMsgIndex = dayEntries.findIndex(
      (item) => item.type === "linkedinMsg",
    );

    if (subject) {
      // Update email
      if (emailIndex !== -1) {
        aiGeneratedLeads[leadIndex][dayIndex][emailIndex].body = editorContent;
        aiGeneratedLeads[leadIndex][dayIndex][emailIndex].subject = subject;

        if (strippedEditorContent === replacedBody) {
          setBody(body);
          setSubject(subject);
          setUpdatedEdittedContent(editorContent);
        } else {
          setBody(body);
          setSubject(subject);
        }
      }
    } else {
      // Update LinkedIn message
      if (linkedinMsgIndex !== -1) {
        aiGeneratedLeads[leadIndex][dayIndex][linkedinMsgIndex].body =
          editorContent;
        aiGeneratedLeads[leadIndex][dayIndex][linkedinMsgIndex].subject =
          subject;
        // setBody(body);
        if (strippedEditorContent === replacedBody) {
          setBody(body);
          setSubject(subject);
          setUpdatedEdittedContent(editorContent);
        } else {
          setBody(body);
          setSubject(subject);
        }
      }
    }
  };

  function getEditorContent(
    leadIndex: number,
    dayIndex: number,
    emailIndex: number,
  ): string | undefined {
    return editorContentMapping.find(
      (mapping) =>
        mapping.leadIndex === leadIndex &&
        mapping.dayIndex === dayIndex &&
        mapping.emailIndex === emailIndex,
    )?.content;
  }

  //   const handleDayUpdate = (
  //     leadIndex,
  //     dayIndex,
  //     body,
  //     subject,
  //     editorContent,
  //     index
  //   ) => {
  //     let editorContentMapping = [];
  //     const dayEntries = aiGeneratedLeads[leadIndex][dayIndex];
  //     console.log(leadIndex, dayIndex)
  //     const strippedEditorContent = stripHtml(editorContent)

  //     const replacedBody =  body.replaceAll("\n", "");

  //     // Find the index for email and LinkedIn message
  //     const emailIndex = dayEntries.findIndex((item) => item.type === "email");
  //     const linkedinMsgIndex = dayEntries.findIndex(
  //       (item) => item.type === "linkedinMsg",
  //     );

  //     if (subject) {
  //       // Update email
  //       if (emailIndex !== -1) {
  //         aiGeneratedLeads[leadIndex][dayIndex][emailIndex].body = body;
  //         aiGeneratedLeads[leadIndex][dayIndex][emailIndex].subject = subject;

  //         if (strippedEditorContent === replacedBody) {
  //           setBody(body);
  //           setSubject(subject);
  //           setUpdatedEdittedContent(editorContent);

  //           // Push the editor content to the mapping array
  //           editorContentMapping.push({
  //               leadIndex: leadIndex,
  //               dayIndex: dayIndex,
  //               emailIndex: emailIndex,
  //               content: editorContent
  //           });

  //       } else {
  //           setBody(body);
  //           setSubject(subject);
  //           setUpdatedEdittedContent("");

  //           // Push the editor content to the mapping array with empty content
  //           editorContentMapping.push({
  //               leadIndex: leadIndex,
  //               dayIndex: dayIndex,
  //               emailIndex: emailIndex,
  //               content: ""
  //           });
  //       }
  //       }
  //     }

  //     else {
  //       // Update LinkedIn message
  //       if (linkedinMsgIndex !== -1) {
  //         aiGeneratedLeads[leadIndex][dayIndex][linkedinMsgIndex].body = body;
  //         aiGeneratedLeads[leadIndex][dayIndex][linkedinMsgIndex].subject =
  //           subject;
  //         setBody(body);
  //       }
  //     }
  //   };

  //   function getEditorContent(leadIndex: number, dayIndex: number, emailIndex: number): string | undefined {
  //     return editorContentMapping.find(mapping =>
  //         mapping.leadIndex === leadIndex &&
  //         mapping.dayIndex === dayIndex &&
  //         mapping.emailIndex === emailIndex
  //     )?.content;
  // }

  function reGenerateContent(body: string, subject: string) {
    if (!subject || subject == "") {
      const linkedInBody = {
        configId: "6629fd49faa87bdbb7f9ded5",
        content: "linkedin",
        body: body,
      };

      axiosPost(
        `/configurations/regenerateContent?responses=${2}`,
        linkedInBody,
      )
        .then((response) => {
          if (response.status === 200) {
            setEditEmailTable(false);
            setShowGenWithAiModal(true);
            let tempArr: any = [];
            response.data.choices.forEach((data, index) => {
              // i want to remove the subject and push the temparr
              if (!subject) {
                tempArr.push({ body: data.body, subject: "" });
              }
            });
            setGeneratedData(tempArr);
          }
          queryClient.invalidateQueries({
            queryKey: ["getCompany"],
          });
        })
        .catch((err) => {
          setLoader(false);
          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "error",
              message:
                err.response?.data?.message ??
                err.message ??
                "Something went wrong, please try again.",
            }),
          );
        });
    } else {
      const emailBody = {
        configId: "6629fd49faa87bdbb7f9ded5",
        content: "email",
        body: body,
        subject: subject,
      };

      axiosPost(`/configurations/regenerateContent?responses=${2}`, emailBody)
        .then((response) => {
          if (response.status === 200) {
            setEditEmailTable(false);
            setShowGenWithAiModal(true);
            let tempArr: any = [];
            response.data.choices.forEach((data, index) => {
              tempArr.push(data);
            });
            setGeneratedData(tempArr);
          }
          queryClient.invalidateQueries({
            queryKey: ["getCompany"],
          });
        })
        .catch((err) => {
          setLoader(false);
          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "error",
              message:
                err.response?.data?.message ??
                err.message ??
                "Something went wrong, please try again.",
            }),
          );
        });
    }
  }

  function handleSmallPopUp(subject, body, type, leadIndex, dayIndex) {
    const strippedEditorContent = stripHtml(updatedEdittedContent);

    const replacedBody = body.replaceAll("\n", "");

    if (type === "linkedinMsg") {
      setBody(body);
      setSubject("");
      setMessageType("linkedinMsg");
    } else {
      if (strippedEditorContent == replacedBody) {
        setBody(updatedEdittedContent);
        setMessageType("email");
        setSubject(subject);
      } else {
        setMessageType("email");
        setSubject(subject);
        setBody(body);
      }
    }
    setLeadIndex(leadIndex);
    setDayIndex(dayIndex);
    setEditEmailTable(true);
  }

  function handleRestAIGen() {
    if (disableGeneration) return;
    generateFollowUpAIContent();
    onClose();
  }

  return (
    <div className="h-100 gen-email-modal days_table_wrapper">
      <div className="all-template-body fx fx-d-col pd-20 gap-15">
        <div className="ob-table-head fx jstfy-sp-btw al-fx-cnt gap-10">
          <h3>
            {aiGeneratedLeads.length} Result
            {aiGeneratedLeads.length > 1 ? "s" : ""}
          </h3>
          {/* {
            <div className="days_wrapper">
              <ul className="day_tabs fx al-cnt">
                {days.map((day) => (
                  <li
                    className={`cp ${currentDay === day ? "active_tab" : ""}`}
                    onClick={() => handleDayChange(day)}
                  >
                    <span>Day {day}</span>
                  </li>
                ))}
              </ul>
            </div>
          } */}
          <div
            className="maxmin-btn br-5 fx al-cnt cursor-pointer gap-8"
            onClick={() => setExpandTable(!expandTable)}
          >
            {expandTable ? <MinIcon /> : <MaxIcon />}
            {expandTable ? "Minimize" : "Maximize"} Table
          </div>
        </div>
        <table className="ob-table no-mt br-5 fx fx-d-col new-scrollbar">
          <thead className="fx p-sticky top-0">
            <tr
              style={{
                gridTemplateColumns: `65px 170px repeat(${Object.keys(formattedScheduleData).length}, 1fr) ${
                  aiGeneratedLeads?.some((lead) => lead.jobTitle)
                    ? "minmax(200px, 1fr)"
                    : ""
                } ${
                  aiGeneratedLeads?.some((lead) => lead.location)
                    ? "minmax(200px, 1fr)"
                    : ""
                } ${
                  aiGeneratedLeads?.some((lead) => lead.company)
                    ? "minmax(200px, 1fr)"
                    : ""
                }`,
              }}
            >
              <th className="opacity-50">NO.</th>
              <th className="opacity-50">Full Name</th>

              {Object.keys(formattedScheduleData)
                .sort(
                  (a, b) =>
                    parseInt(a.replace("day_", "")) -
                    parseInt(b.replace("day_", "")),
                )
                .map((day, index) => (
                  <th
                    key={index}
                    className={`${
                      formattedScheduleData[day]?.some(
                        (action) =>
                          action.type === "linkedinMsg" ||
                          action.type === "email",
                      )
                        ? "w-mc"
                        : "small_width"
                    } ${
                      formattedScheduleData[day]?.some(
                        (action) =>
                          (Object.keys(formattedScheduleData).length === 1 &&
                            expandTable &&
                            formattedScheduleData[day].length <= 2 &&
                            action.type === "email") ||
                          (expandTable &&
                            formattedScheduleData[day].length <= 2 &&
                            action.type === "linkedinMsg"),
                      )
                        ? "w-100"
                        : ""
                    }`}
                  >
                    {`Day ${parseInt(day.replace("day_", ""))}`}
                    <ul className="fx action_ul mt-10">
                      {(() => {
                        const actions = formattedScheduleData[day];
                        const showLinkedIn =
                          actions.length === 2 &&
                          !actions?.some(
                            (action) =>
                              action.type === "linkedinMsg" ||
                              action.type === "email",
                          );

                        if (showLinkedIn) {
                          return (
                            <li
                              key="linkedin"
                              className="action_li"
                              style={{ width: "155px" }}
                            >
                              LinkedIn
                            </li>
                          );
                        } else {
                          return actions.map((action, actionIndex) => (
                            <li
                              key={actionIndex}
                              className={`action_li fs-0 ${Object.keys(formattedScheduleData).length === 1 && expandTable && actions.length <= 2 && (action.type === "email" || action.type === "linkedinMsg") ? "w-100" : ""}`}
                              style={{
                                width:
                                  action.type === "email" ||
                                  action.type === "linkedinMsg"
                                    ? "540px"
                                    : "155px",
                                flexShrink:
                                  action.type === "email" ||
                                  action.type === "linkedinMsg"
                                    ? "unset !important"
                                    : "",
                              }}
                            >
                              {formatType(action.type)}
                            </li>
                          ));
                        }
                      })()}
                    </ul>
                  </th>
                ))}

              {aiGeneratedLeads?.some((lead) => lead.jobTitle) && (
                <th className="common_info">Title</th>
              )}
              {aiGeneratedLeads?.some((lead) => lead.location) && (
                <th className="common_info">Location</th>
              )}
              {aiGeneratedLeads?.some((lead) => lead.company) && (
                <th className="common_info">Company</th>
              )}
            </tr>
          </thead>

          <tbody className="fx fx-d-col">
            {aiGeneratedLeads?.length ? (
              aiGeneratedLeads?.map((lead, leadIndex) => (
                <tr
                  key={leadIndex}
                  style={{
                    gridTemplateColumns: `65px 170px repeat(${Object?.keys(formattedScheduleData).length}, 1fr) ${
                      lead.jobTitle ? "minmax(200px, 1fr)" : ""
                    } ${lead.location ? "minmax(200px, 1fr)" : ""} ${
                      lead.company ? "minmax(200px, 1fr)" : ""
                    }`,
                  }}
                >
                  <td className="fw-400 jstfy-cnt sr_no_tr opacity-50">
                    {(leadIndex + 1).toString().padStart(2, "0")}
                  </td>
                  <td className="opacity-50">{lead.name}</td>
                  {Object?.keys(formattedScheduleData)?.map(
                    (dayKey, dayIndex) => (
                      <td
                        key={dayIndex}
                        className={`${
                          formattedScheduleData[dayKey]?.some(
                            (action) =>
                              action.type === "linkedinMsg" ||
                              action.type === "email",
                          )
                            ? "w-mc"
                            : "small_width"
                        } ${
                          Object?.keys(formattedScheduleData).length === 1 &&
                          expandTable &&
                          formattedScheduleData[dayKey]?.length <= 2 &&
                          formattedScheduleData[dayKey]?.some(
                            (action) =>
                              action.type === "email" ||
                              action.type === "linkedinMsg",
                          )
                            ? "w-100"
                            : ""
                        }`}
                      >
                        <ul
                          style={{
                            marginInlineStart: formattedScheduleData[
                              dayKey
                            ]?.some(
                              (action) =>
                                action.type === "linkedinMsg" ||
                                action.type === "email",
                            )
                              ? ""
                              : "-8px",
                          }}
                          className={`fx action_ul h-100 ${
                            formattedScheduleData[dayKey]?.some(
                              (action) =>
                                action.type === "linkedinMsg" ||
                                action.type === "email",
                            )
                              ? ""
                              : "fx-d-col al-cnt no_border_r jstfy-cnt"
                          }`}
                        >
                          {formattedScheduleData[dayKey]?.map(
                            (scheduledAction, actionIndex) => {
                              const leadAction = (lead[dayKey] || []).find(
                                (action) =>
                                  action.type === scheduledAction.type,
                              );
                              return (
                                <li
                                  key={actionIndex}
                                  style={{
                                    width:
                                      scheduledAction.type === "email" ||
                                      scheduledAction.type === "linkedinMsg"
                                        ? "540px"
                                        : "155px",
                                    flexShrink:
                                      scheduledAction.type === "email" ||
                                      scheduledAction.type === "linkedinMsg"
                                        ? "unset !important"
                                        : "",
                                  }}
                                  className={`action_li fs-0 ${
                                    scheduledAction.type === "email" ||
                                    scheduledAction.type === "linkedinMsg"
                                      ? "action_li_hover"
                                      : ""
                                  } ${Object?.keys(formattedScheduleData).length === 1 && expandTable && formattedScheduleData[dayKey].length <= 2 && (scheduledAction.type === "email" || scheduledAction.type === "linkedinMsg") ? "w-100" : ""}`}
                                >
                                  <div
                                    className={`w-100 fx fx-d-col jstfy-strt al-cnt ${
                                      scheduledAction.type === "email" ||
                                      scheduledAction.type === "linkedinMsg"
                                        ? ""
                                        : "w-mc"
                                    }`}
                                  >
                                    {scheduledAction.type === "email" ? (
                                      <div className="fx w-100 al-cnt linkedin_msg_textarea email_textarea">
                                        <div className="fx fx-d-col w-100 gap-15">
                                          <div className="fx fx-d-col">
                                            <span>Subject:</span>
                                            <input
                                              value={
                                                leadAction
                                                  ? leadAction.subject
                                                  : ""
                                              }
                                              readOnly
                                            />
                                          </div>
                                          <div className="fx fx-d-col">
                                            <span>Body:</span>
                                            <div
                                              className="email_body_clamp"
                                              dangerouslySetInnerHTML={{
                                                __html:
                                                  leadAction?.body?.replace(
                                                    /\\n(?!\s|$)/g,
                                                    "<br>",
                                                  ) ?? "",
                                              }}
                                            />
                                          </div>
                                        </div>
                                        <span
                                          onClick={() =>
                                            handleSmallPopUp(
                                              leadAction
                                                ? leadAction.subject
                                                : "",
                                              leadAction
                                                ? leadAction.body.replace(
                                                    /\\n(?!\s|$)/g,
                                                    "<br>",
                                                  )
                                                : "",
                                              scheduledAction.type,
                                              leadIndex,
                                              dayKey,
                                            )
                                          }
                                          className="edit_icon cp"
                                        >
                                          <EditEmailIconSvg />
                                        </span>
                                      </div>
                                    ) : scheduledAction.type ===
                                      "linkedinMsg" ? (
                                      <div className="fx w-100 linkedin_msg_textarea al-cnt">
                                        <div
                                          className="email_body_clamp"
                                          dangerouslySetInnerHTML={{
                                            __html:
                                              leadAction?.body?.replace(
                                                /\\n(?!\s|$)/g,
                                                "<br>",
                                              ) ?? "",
                                          }}
                                        />
                                        <span
                                          onClick={() =>
                                            handleSmallPopUp(
                                              leadAction
                                                ? leadAction.subject
                                                : "",
                                              leadAction ? leadAction.body : "",
                                              scheduledAction.type,
                                              leadIndex,
                                              dayKey,
                                            )
                                          }
                                          className="edit_icon cp"
                                        >
                                          <EditEmailIconSvg />
                                        </span>
                                      </div>
                                    ) : (
                                      <span>
                                        {formatType(scheduledAction.type)}
                                      </span>
                                    )}
                                  </div>
                                </li>
                              );
                            },
                          )}
                        </ul>
                      </td>
                    ),
                  )}
                  {lead.jobTitle && (
                    <td className="common_info">{lead.jobTitle}</td>
                  )}
                  {lead.location && (
                    <td className="common_info">{lead.location}</td>
                  )}
                  {lead.company && (
                    <td className="common_info">{lead.company}</td>
                  )}
                </tr>
              ))
            ) : (
              <div className="no_data_found">
                <NoDataFound />
                <h1>No data found</h1>
              </div>
            )}
          </tbody>
        </table>
      </div>
      <div className={`call-api-footer fx-d-row fx-d-col gap-10`}>
        {AIContentStep !== "loading" && AIContentStep !== "followup" && (
          <p className="text-center">
            Edit the content if needed, then click approve.
          </p>
        )}

        {AIContentStep === "followup" ? (
          <div className="fx gap-20">
            {workflowLoader ? (
              <div className="load-container">
                <div className="linespinner"></div>
              </div>
            ) : (
              <>
                <button
                  className="cancel-btn fw-400 f-14 w-mc"
                  onClick={sendCampaign}
                >
                  Send Campaign
                </button>
                <button
                  className="save-btn fw-400 f-14 w-mc ob-btn-gradient"
                  onClick={editInBuilder}
                >
                  Edit In Builder
                </button>
              </>
            )}
          </div>
        ) : AIContentStep === "loading" ? (
          <div className="load-container">
            <div className="linespinner"></div>
          </div>
        ) : (
          <div className="fx gap-10">
            <button
              className="save-btn fw-400 f-14 w-mc ob-btn-gradient btn-agent"
              onClick={handleRestAIGen}
              disabled={disableGeneration}
            >
              Approve
            </button>
            <button className="cancel-btn" onClick={onClose}>
              Return to chat
            </button>
          </div>
        )}
      </div>

      {editEmailTable && (
        <EmailBodyModal
          setEditEmailTable={setEditEmailTable}
          body={body}
          setBody={setBody}
          subject={subject}
          setSubject={setSubject}
          onSave={(subject, body, editorContent, index) => {
            handleDayUpdate(leadIndex, dayIndex, body, subject, editorContent);
          }}
          setShowGenWithAiModal={setShowGenWithAiModal}
          generateContent={reGenerateContent}
          allData={aiGeneratedLeads[leadIndex]}
          messageType={messageType}
          loaders={loader}
          setloaders={setLoader}
        />
      )}
      {showGenWithAiModal && (
        <GenWithAiModal
          nodeName="generateEmail"
          setEditEmailTable={setEditEmailTable}
          setShowGenWithAiModal={setShowGenWithAiModal}
          onBoarding={true}
          generatedData={generatedData}
          onSave={(subject, body, editorContent) => {
            handleDayUpdate(leadIndex, dayIndex, body, subject, editorContent);
          }}
          onClose={() => {
            setShowGenWithAiModal(false);
          }}
        />
      )}
    </div>
  );
};

