import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { userSlice } from "@/redux/User";

const CreditsModal = ({ setChat, trial, creditInSufficient }) => {
  const router = useNavigate();
  const dispatch = useDispatch();
  const handleNavigation = () => {
    router("/settings");
    dispatch(userSlice.actions.setSettings(trial ? 2 : 6));
  };
  return (
    <div className="createbot-modal leave-bot-modal get_credits_modal">
      <div className="craetebot-container">
        <div className="success-cont leave-modal">
          <div className="err-header fx-d-col gap-8">
            <CreditIcon />
            <h1 className="mt-3">Ran out of Credits</h1>
            {creditInSufficient ? (
              <p> Your credits are insufficient for this request!</p>
            ) : trial ? (
              <p>Buy plan to be able to continue with your workflows</p>
            ) : (
              <p>Add credits to be able to continue with your workflow</p>
            )}
          </div>
          <div className="succ-footer">
            <div className="fx">
              <button onClick={() => setChat(false)} className="leave-btn mt-0">
                Continue with chat
              </button>
              <button onClick={handleNavigation} className="stay-btn mt-0">
                {creditInSufficient
                  ? "Get More Credits"
                  : trial
                    ? "Get Credits"
                    : "Add More Credits"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreditsModal;

const CreditIcon = () => (
  <svg
    width={50}
    height={50}
    viewBox="0 0 50 50"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      fillRule="evenodd"
      clipRule="evenodd"
      d="M24.9979 50H25.0022C38.7863 49.9988 50 38.7884 50 25C50 11.2116 38.7852 0 25 0C11.2148 0 0 11.2109 0 25C0 38.7891 11.2137 49.9988 24.9979 50ZM27.5127 35.8697C27.0538 36.8309 25.6103 36.504 25.6103 35.4389V28.6304C25.6099 28.3867 25.5129 28.1531 25.3405 27.9808C25.1682 27.8086 24.9346 27.7116 24.6909 27.7113H17.5899C16.8545 27.7113 16.3706 26.9442 16.6874 26.2805L22.4877 14.1304C22.9466 13.1691 24.3901 13.496 24.3901 14.5612V21.3696C24.3904 21.6133 24.4873 21.8468 24.6596 22.0191C24.8318 22.1914 25.0654 22.2884 25.309 22.2888H32.4104C33.1458 22.2888 33.6297 23.0559 33.3129 23.7196L27.5127 35.8697Z"
      fill="#FB5F66"
    />
  </svg>
);
