import { Fragment, useState } from "react";
import _ from "lodash";
import { PeopleSVG, CompanySVG } from "@/assets/Audience Images/ApolloSVG1";
import LinkedinP from "../audience/list/LinkedinP";
import LinkedinC from "../audience/list/LinkedinC";
import { motion } from "framer-motion";
import "../audience/audience.css";

export function LinkedInLeads({
  onReload,
  type,
  selectedList,
  isCynthia,
  setCompanyLeads,
  setEmployeeLeads,
  employeeLeads,
  setExpandTable,
  dataLead,
  targetAudience,
  setTargetAudience,
  isEdit,
  setIsEdit,
  onClose,
  onSave,
  setSideBarProgress,
  compDataLead,
}) {
  const [activePeople, setActivePeople] = useState(isCynthia ? 1 : 0);
  const [activeCompany, setActiveCompany] = useState(0);
  const [isOpen, setIsOpen] = useState(false);

  const [selectedTab, setSelectedTab] = useState(
    type === "company" ? "Company" : "People",
  );
  const toggleDropdown = (e) => {
    e.stopPropagation();
    setIsOpen(!isOpen);
  };

  const saveSelectedLeadsCompany = (leads) => {
    setSideBarProgress(2);
    setCompanyLeads(leads);
    setExpandTable(false);
    onSave();
  };

  const saveSelectedLeadsCompanyi = (leads) => {
    setSideBarProgress(2);
    setCompanyLeads(leads);
  };

  const saveSelectedLeadsPeople = (leads) => {
    setSideBarProgress(3);
    setEmployeeLeads(leads);
    setExpandTable(false);
    onSave();
  };

  return (
    <>
      <div className="createbot-modal">
        <div className="craetebot-container p-relative">
          <div
            className="close lead-close new-modal-close flex-cnt"
            onClick={onClose}
          >
            <svg
              width="10"
              height="10"
              viewBox="0 0 10 10"
              fill="#6D6D6D"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                fill="#6D6D6D"
              ></path>
            </svg>
          </div>
          <motion.div
            initial={{ y: 0, opacity: 0 }}
            animate={{ y: 10, opacity: 1 }}
            exit={{ y: 100, opacity: 0 }}
            className="lead-modal"
          >
            {/* make this on condition base */}
            <div className="lead-modal-head pd-20 grid gap-20">
              {/* {!isCynthia && (
                <h2>Find {type == "linkedin-people" ? "People" : "Company"}</h2>
              )} */}
              <div className="toggle-btn fx">
                {type === "create" && (
                  <Fragment>
                    <button
                      className={`flex-cnt no-m gap-8 ${selectedTab === "People" && "btn-active"}`}
                      onClick={() => setSelectedTab("People")}
                    >
                      <PeopleSVG /> People
                    </button>
                    <button
                      className={`flex-cnt no-m gap-8 ${selectedTab === "Company" && "btn-active"}`}
                      onClick={() => setSelectedTab("Company")}
                    >
                      <CompanySVG /> Company
                    </button>
                  </Fragment>
                )}
                {(type == "linkedin-people" || type === "people") && (
                  <button className="flex-cnt no-m btn-active people_list_btn gap-8">
                    <PeopleSVG /> Find People
                  </button>
                )}
                {(type == "linkedin-company" || type === "company") && (
                  <button
                    className="flex-cnt no-m btn-active company_list_btn gap-8 "
                    style={{ cursor: "auto" }}
                  >
                    <CompanySVG /> Find Companies
                  </button>
                )}
              </div>
            </div>
            {selectedTab === "People" && (
              <LinkedinP
                onClose={() => {}}
                onReload={onReload}
                type={type}
                selectedList={selectedList}
                isCynthia={isCynthia}
                saveSelectedLeads={saveSelectedLeadsPeople}
                dataLead={dataLead}
                employeeLeads={employeeLeads}
                isEdit={isEdit}
                setIsEdit={setIsEdit}
                setCompanyDataLead={setCompanyLeads}
                compDataLead={compDataLead}
                saveCompany={saveSelectedLeadsCompanyi}
              />
            )}
            {selectedTab === "Company" && (
              <LinkedinC
                onClose={() => {}}
                onReload={onReload}
                type={type}
                selectedList={selectedList}
                isCynthia={isCynthia}
                saveSelectedLeads={saveSelectedLeadsCompany}
                dataLead={dataLead}
                targetAudience={targetAudience}
                setTargetAudience={setTargetAudience}
                isEdit={isEdit}
                setIsEdit={setIsEdit}
              />
            )}
          </motion.div>
        </div>
      </div>
    </>
  );
}
