import { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";

interface Props {
  message: string;
}

export default function AssistantMessage({ message }: Props) {
  if (message === "") return null;

  return (
    <div className="chat-right fx gap-10">
      <div className="bot-avtar">
        <img
          src="https://dlvkyia8i4zmz.cloudfront.net/0Lz2SJJRbOaPZxVsD55t_Avtar.png"
          alt=""
        />
      </div>
      <div className="rght-message onboard-msg">
        <ReactMarkdown children={message} remarkPlugins={[remarkGfm]} />
      </div>
    </div>
  );
}

