import { useMemo } from "react";
import * as Cynthia from "../cynthia/schema";

export default function useAgent(agentCode: string) {
  const agentData = useMemo(() => {
    const { startNodeId, nodes, edges } = Cynthia;

    return {
      nodes,
      edges,
      startNodeId,
    };
  }, [agentCode]);

  return agentData;
}
