import { useState } from "react";

interface Props {
  companyData: Record<string, any>;
  setCompanyData: (data: Record<string, any>) => void;
  onProceed?: (option: string, text: string) => void;
}

export default function AgentCompanyData({
  companyData,
  setCompanyData,
  onProceed,
}: Props) {
  const [editingField, setEditingField] = useState<string | null>(null);

  function stripUrl(url: string) {
    return url.replace(/^(https?:\/\/)?(www\.)?/, "");
  }
  const showOptions = !!onProceed;

  function handleEditSelect(event, _editingField) {
    event.stopPropagation();
    // setEditingField(editingField ? null : _editingField);
    setEditingField(_editingField);
  }

  function handleCompanyDataChange(key: string, value: string) {
    setCompanyData({
      ...companyData,
      [key]: value,
    });
  }

  return (
    <div
      className={`fx fx-d-col al-fx-start fx-strt agent_data gap-10 ${!showOptions ? "ml-43" : ""}`}
    >
      <div className="comp-outer">
        <table className="agent-company-data">
          <tr>
            <th>Details</th>
            <th>Response</th>
            <th>{showOptions && "Edit"}</th>
          </tr>
          <tr>
            <td className="al-cnt fx">Website URL</td>
            <td className="al-cnt fx">
              {editingField === "companyWebsite" ? (
                <input
                  type="text"
                  value={companyData.companyWebsite}
                  onChange={(event) =>
                    handleCompanyDataChange(
                      "companyWebsite",
                      event.target.value,
                    )
                  }
                />
              ) : (
                <a
                  href={"https://" + stripUrl(companyData.companyWebsite)}
                  target="_blank"
                >
                  {companyData.companyWebsite}
                </a>
              )}
            </td>
            <td className="al-cnt fx">
              {showOptions && (
                <GreyEditIcon
                  className="cp"
                  onClick={(event) => handleEditSelect(event, "companyWebsite")}
                />
              )}
            </td>
          </tr>
          <tr>
            <td className="al-cnt fx">Main Problem</td>
            <td className="al-cnt fx">
              {editingField === "customerPainPointAndUniqueValueProposition" ? (
                <input
                  type="text"
                  value={companyData.customerPainPointAndUniqueValueProposition}
                  onChange={(event) =>
                    handleCompanyDataChange(
                      "customerPainPointAndUniqueValueProposition",
                      event.target.value,
                    )
                  }
                />
              ) : (
                <p>{companyData.customerPainPointAndUniqueValueProposition}</p>
              )}
            </td>
            <td className="al-cnt fx">
              {showOptions && (
                <GreyEditIcon
                  className="cp"
                  onClick={(event) =>
                    handleEditSelect(
                      event,
                      "customerPainPointAndUniqueValueProposition",
                    )
                  }
                />
              )}
            </td>
          </tr>
          <tr>
            <td className="al-cnt fx">Testimonials</td>
            <td className="al-cnt fx">
              {editingField === "customerSuccessStory" ? (
                <input
                  type="text"
                  value={companyData.customerSuccessStory}
                  onChange={(event) =>
                    handleCompanyDataChange(
                      "customerSuccessStory",
                      event.target.value,
                    )
                  }
                />
              ) : (
                <p>{companyData.customerSuccessStory}</p>
              )}
            </td>
            <td className="al-cnt fx">
              {showOptions && (
                <GreyEditIcon
                  className="cp"
                  onClick={(event) =>
                    handleEditSelect(event, "customerSuccessStory")
                  }
                />
              )}
            </td>
          </tr>
        </table>
      </div>
      {showOptions && (
        <>
          <div className="rght-message onboard-msg">
            <p>Would you like to:</p>
          </div>
          <div className="fx gap-10">
            <div
              className={`connect_int p-relative w-mc cp csv_upload_int`} //${selectedOption === "b" ? "active_int" : ""}
              onClick={() => onProceed("b", "Start from scratch")}
            >
              <div className="connect_int_inner">
                <div className="pseudo-radio"></div>
                <div className="connect_int_name">
                  <EditIcon />
                  <span>Start from scratch</span>
                </div>
              </div>
            </div>
            <div
              className={`connect_int p-relative w-mc cp csv_upload_int`} //${selectedOption === "a" ? "active_int" : ""}
              onClick={() => onProceed("a", "Continue")}
            >
              <div className="connect_int_inner">
                <div className="pseudo-radio"></div>
                <div className="connect_int_name">
                  <ArrowIcon />
                  <span>Continue</span>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
}

