import { useEffect, useState } from "react";
import LinkedIn from "@/components/integration-modals/LinkedIn";
import "@/styles/config.css";
import "@/styles/builder.css";

interface Props {
  onSuccess: (config: any) => void;
  onClose: () => void;
  openLinkedIn?: any;
}

function LinkedinConnect({ onSuccess, onClose }: Props): JSX.Element {
  const [openModalId, setOpenModalId] = useState<ModalId>("");
  const [selectedConfigId, setSelectedConfigId] = useState<ModalId>("");

  const handleConfig = (config: ModalId) => {
    setOpenModalId(config);
    setSelectedConfigId(config);
  };

  return (
    <div>
      <div
        className={`connect_int p-relative w-mc cp ${selectedConfigId === "linkedin" ? "active_int" : ""}`}
        onClick={() => handleConfig("linkedin")}
      >
        <div className="connect_int_inner">
          <div className="pseudo-radio"></div>
          <div className="connect_int_name">
            <LinkedinIcon />
            <span>Linkedin</span>
          </div>
        </div>
      </div>
      {openModalId === "linkedin" && (
        <LinkedIn
          onSuccess={(config) => {
            setOpenModalId("");
            onSuccess(config);
          }}
          onClose={() => {
            setOpenModalId("");
            setSelectedConfigId("");
            onClose();
          }}
        />
      )}
    </div>
  );
}

export default LinkedinConnect;

