import { useEffect, useState, useRef } from "react";
import Smtp from "@/components/integration-modals/Smtp";
import Gmail from "@/components/integration-modals/Gmail";
import "@/styles/config.css";
import "@/styles/builder.css";
import { axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";

interface Props {
  emailConfig?: any;
  onSuccess: (config: any) => void;
  onClose: () => void;
  espConnected?: string[];
}

function WarmyConnect({
  emailConfig,
  onSuccess,
  onClose,
  espConnected = [],
}: Props) {
  const [selectedConfigId, setSelectedConfigId] = useState<ModalId>("");
  const dispatch = useDispatch();
  const firstRender = useRef(true);
  const calledOnce = useRef(false);

  const handleOathLogin = (config: ModalId) => {
    if (calledOnce.current) return;
    setSelectedConfigId(config);

    axiosPost(`/configurations/warmy?configId=${emailConfig?._id}`)
      .then((res) => {
        console.log("Axios response:", res);
        if (res.data) {
          onSuccess(res.data);
          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "success",
              message: `Email warming up successfully`,
            }),
          );
        } else {
          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "error",
              message: "Unable to warm up email account",
            }),
          );
        }
      })
      .catch((err) => {
        console.error("Axios error:", err);
        onClose();
      });

    calledOnce.current = true;
  };

  useEffect(() => {
    console.log("useEffect triggered");
    if (firstRender.current) {
      firstRender.current = false;
      if (espConnected.length === 1) {
        handleOathLogin(emailConfig?.esp as ModalId);
        setSelectedConfigId(emailConfig?.esp as ModalId);
      }
    }
  }, []);

  return (
    <div className="chat-right fx al-cnt gap-15">
      {espConnected.includes("gmail") && (
        <div
          className={`connect_int p-relative w-mc cp ${selectedConfigId === "gmail" ? "active_int" : ""}`}
          onClick={() => handleOathLogin("gmail")}
        >
          <div className="connect_int_inner">
            <div className="pseudo-radio"></div>
            <div className="connect_int_name">
              <GmailIcon />
              <span>Gmail </span>
            </div>
          </div>
        </div>
      )}
      {espConnected.includes("smtp") && (
        <div
          className={`connect_int p-relative w-mc cp ${selectedConfigId === "smtp" ? "active_int" : ""}`}
          onClick={() => handleOathLogin("smtp")}
        >
          <div className="connect_int_inner">
            <div className="pseudo-radio"></div>
            <div className="connect_int_name">
              <SmptIcon />
              <span>SMTP</span>
            </div>
          </div>
        </div>
      )}
      {espConnected.includes("outlook") && (
        <div
          className={`connect_int p-relative w-mc cp ${selectedConfigId === "outlook" ? "active_int" : ""}`}
          onClick={() => handleOathLogin("outlook")}
        >
          <div className="connect_int_inner">
            <div className="pseudo-radio"></div>
            <div className="connect_int_name">
              <OutlookIcon />
              <span>Outlook</span>
            </div>
          </div>
        </div>
      )}
      {espConnected.includes("linkedin") && (
        <div
          className={`connect_int p-relative w-mc cp ${selectedConfigId === "linkedin" ? "active_int" : ""}`}
          onClick={() => handleOathLogin("linkedin")}
        >
          <div className="connect_int_inner">
            <div className="pseudo-radio"></div>
            <div className="connect_int_name">
              <LinkedinIcon />
              <span>LinkedIn</span>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

