import { useCallback, useEffect, useState } from "react";
import {
  ReactFlow,
  Controls,
  Background,
  applyNodeChanges,
  applyEdgeChanges,
  addEdge,
  useReactFlow,
  Panel,
} from "@xyflow/react";
import "@xyflow/react/dist/style.css";
import { nodeTypes } from "../omni-builder/react-flow/config";
import { useNavigate } from "react-router-dom";
import { axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { reactFlowToDb } from "../omni-builder/utils/botTransformer";

import dagre from "dagre";

const NODE_WIDTH = 272;
const NODE_HEIGHT = 156;

export default function BuilderPreview({
  workflowNodes,
  setWorkflowNodes,
  workflowEdges,
  setWorkflowEdges,
}) {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const reactFlow = useReactFlow();
  const [botSaveLoader, setBotSaveLoader] = useState(false);

  useEffect(() => {
    reactFlow.setViewport({
      x: 0,
      y: 0,
      zoom: 1,
    });
    // autoLayout();
  }, []);

  const autoLayout = useCallback(() => {
    const dagreGraph = new dagre.graphlib.Graph();
    dagreGraph.setDefaultEdgeLabel(() => ({}));
    dagreGraph.setGraph({
      align: "DL",
      rankdir: "TB",
      ranker: "longest-path",
    });

    for (const node of workflowNodes) {
      dagreGraph.setNode(node.id, {
        width: node.width ?? NODE_WIDTH,
        height: node.height ?? NODE_HEIGHT,
      });
    }

    for (const edge of workflowEdges) {
      dagreGraph.setEdge(edge.source, edge.target);
    }

    dagre.layout(dagreGraph);

    const newNodes: Array<Node> = [];
    for (const node of workflowNodes) {
      const nodeWithPosition = dagreGraph.node(node.id);

      newNodes.push({
        ...node,
        position: {
          x: nodeWithPosition.x - nodeWithPosition.width / 2,
          y: nodeWithPosition.y - nodeWithPosition.height / 2,
        },
      });
    }
    setWorkflowNodes(newNodes);

    reactFlow.setViewport({
      x: 0,
      y: 0,
      zoom: 1,
    });
  }, [workflowNodes, workflowEdges]);

  function handleNodesChange(changes) {
    const newNodes = applyNodeChanges(changes, workflowNodes);
    setWorkflowNodes(newNodes);
  }

  function handleEdgesChange(changes) {
    const newEdges = applyEdgeChanges(changes, workflowEdges);
    setWorkflowEdges(newEdges);
  }

  function handleConnect(connection) {
    const newEdges = addEdge(connection, workflowEdges);
    setWorkflowEdges(newEdges);
  }

  async function saveWorkflow() {
    try {
      if (botSaveLoader) return;
      setBotSaveLoader(true);
      const botData = reactFlowToDb(workflowNodes, workflowEdges, {});

      const response = await axiosPost("/users/createAiGenBot", {
        questions: botData.questions ?? [],
      });
      console.log(response);

      navigate(`/builder/${response.data.data.botData.url}`);
      setBotSaveLoader(false);
    } catch (error: any) {
      console.log(error);
      setBotSaveLoader(false);
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "error",
        message: error.response?.data.message || "Failed to save bot",
      });
    }
  }

  return (
    <ReactFlow
      nodes={workflowNodes}
      edges={workflowEdges}
      onNodesChange={handleNodesChange}
      onEdgesChange={handleEdgesChange}
      onConnect={handleConnect}
      nodeTypes={nodeTypes}
    >
      <Panel
        position="bottom-center"
        className="ob-builder-dashboard-btn-wrapper"
      >
        <button onClick={saveWorkflow}>
          {botSaveLoader ? "Saving..." : "Edit In Builder"}
        </button>
      </Panel>
      <Controls />
      <Background />
    </ReactFlow>
  );
}
