import type { Node } from "@xyflow/react";

interface MsgBase {
  id: string;
}

interface UserMsg extends MsgBase {
  role: "user";
  content: string;
}

interface AssistantMsg extends MsgBase {
  role: "assistant";
  content: string;
}

interface SystemMsg extends MsgBase {
  role: "system";
  type:
    | "companyLeads"
    | "employeeLeads"
    | "scheduleTables"
    | "dayMessage"
    | "agentData";
}

export type Message = UserMsg | AssistantMsg | SystemMsg;

export type PreviewType =
  | "linkedin_company"
  | "linkedin_employee"
  | "apollo_company"
  | "apollo_employee"
  | "csv_instruction"
  | "upload_company"
  | "upload_employee"
  | "dayMessage"
  | "schedule"
  | "builder";

export const SCHEDULE_NODE = {
  email: "Email",
  linkedinVisit: "Linkedin Visit",
  linkedinConnect: "Linkedin Connect",
  linkedinMsg: "Linkedin Message",
} as const;

export type NodeType = keyof typeof SCHEDULE_NODE;

export interface Schedule {
  day: number;
  nodes: Array<NodeType>;
}

export const INIT_SCHEDULE: Array<Schedule> = [
  {
    day: 1,
    nodes: ["linkedinVisit"],
  },
  {
    day: 2,
    nodes: ["email"],
  },
  {
    day: 3,
    nodes: ["linkedinConnect"],
  },
  {
    day: 5,
    nodes: ["linkedinMsg"],
  },
  {
    day: 6,
    nodes: ["email"],
  },
  {
    day: 8,
    nodes: ["email"],
  },
  {
    day: 10,
    nodes: ["linkedinVisit"],
  },
  {
    day: 12,
    nodes: ["email"],
  },
  {
    day: 19,
    nodes: ["email"],
  },
];

export type Action = {
  name: string;
  args?: Record<string, any>;
};

export type AgentButton =
  | {
      type: "text";
      label: string;
      action: Action;
    }
  | {
      type: "pre-defined";
      buttonName: string;
    };

export type QuestionNode = Node<
  {
    title: string;
    buttons?: Array<AgentButton>;
    validate?: boolean;
    regExp?: RegExp;
    saveResponse?: boolean;
    variable?: string;
  },
  "question"
>;

export type ActionNode = Node<
  {
    action: Action;
  },
  "action"
>;

export type AgentNode = QuestionNode | ActionNode;
