import clsx from "clsx";
import { Crisp } from "crisp-sdk-web";
import { AnimatePresence, motion } from "framer-motion";
import { useEffect, useState } from "react";

export default function AgentSidebar({ sideBarProgress, toggleSidebar, openSidebar }) {
  const [isMozilla, setIsMozilla] = useState(false);

  useEffect(() => {
    setIsMozilla(isFirefox());
  }, []);

  
  const isFirefox = () => {
    return /firefox/i.test(navigator.userAgent);
  };
  const handleOpenChat = async () => {
    Crisp.chat.open();
    Crisp.message.send("text", "We'll get back to you shortly");
  };
  return (
    <div
      className={`stepper-onboard fs-0 fx fx-d-col gap-30 p-relative collapse_sidebar   ${openSidebar ? "open_sidebar" : ""}  ${isMozilla ? "transparent_scroll" : ""}`}
    >
      <div className="open p-absolute cp gap-5" onClick={toggleSidebar}>
        <span>{openSidebar ? "COLLAPSE" : "OPEN"}</span> <OpenSideBarIcon rotateVal={openSidebar ? "180deg" : "0deg"} />
      </div>
      <div className="steps_container fx fx-d-col h-100 ovr-flw-au gap-5">
        {Steps.map((step, index) => (
          <div
            className={clsx( 
              "step_wrapper fx gap-15 p-relative",
              !index && sideBarProgress < 1 && "first_step",
              index < sideBarProgress && "done_step",
              index > sideBarProgress && "incoming_step",
            )}
            key={index}
          >
            <motion.div className="step_count grid-cnt fs-0">
              <CompletedStepSvg /> <span>{index + 1}</span>
            </motion.div>
            <motion.div
              initial={{ opacity: 0, translateY: -50 }}
              animate={{ opacity: 1, translateY: 0 }}
              transition={{ duration: 0.4, delay: index * 0.2 }}
              className="step_info fx fx-d-col gap-5"
            >
              <span>{`STEP ${index + 1}`}</span>
              <h2>{step.heading}</h2>
              <p>{step.subHeading}</p>
            </motion.div>
          </div>
        ))}
      </div>
      <div className="contact_us">
        <div className="contact_text">
          <p>Having trouble?</p>
          <motion.button
            whileHover={{ scale: 0.85 }}
            whileTap={{ scale: 1 }}
            className="fx al-cnt"
            onClick={handleOpenChat}
          >
            Contact Us
            <ArrowIcon />
          </motion.button>
        </div>
        <span className="ques_icon cp" onClick={handleOpenChat}>
          <QuestionMarkIcon />
        </span>
      </div>
    </div>
  );
}

const Steps = [
  {
    heading: "User Onboarding",
    subHeading: " Get started seamlessly and set your outreach goals.",
  },
  {
    heading: "Organization Targeting",
    subHeading:
      "Pinpoint the companies and industries that fit your ideal customer.",
  },
  {
    heading: "Lead Generation & Enrichment",
    subHeading: "Find high-potential leads and uncover insights that matter.",
  },
  {
    heading: "Personalized Outreach Drafts",
    subHeading: " Craft tailored messages that resonate with each prospect.",
  },
  {
    heading: "Outreach Setup & Execution",
    subHeading:
      "Automate your outreach and track results for continuous improvement.",
  },
  {
    heading: "Value-Driven Prospect Summary",
    subHeading:
      "(Automatic Public/Private + Branching + Uncertainty Handling + Enhancements)",
  },
];

