import ManageAddons from "@/components/layoutElements/manageAddons";
import Plans from "@/components/layoutElements/plans";
import React, { useEffect, useState } from "react";
interface Props {}
function PlanAddonModal({ openBilling, setOpenBilling, showAddons }) {
  const [showAddonsModal, setShowAddonsModal] = useState<AddonModals>("addons");
  const [showPlansModal, setShowPlansModal] = useState<PlanModals>("plan");

  useEffect(() => {
    console.log(showPlansModal)
  }, [showPlansModal])

  return (
    <>
      <div
        className={`${
          showAddonsModal === "addons"
            ? "createbot-modal modal-scroll agent-plan"
            : ""
        } ${showPlansModal !== "plan" ? "agent-plan-summary" : ""}`}
      >
        <div
          className={
            showAddonsModal === "addons" && showPlansModal === "plan"
              ? "createbot-container modal-responsive create-bot-landscape"
              : ""
          }
        >
          {!showAddons ? (
            <ManageAddons
              calledFromAgent={true}
              showAddonsModal={showAddonsModal}
              setShowAddonsModal={setShowAddonsModal}
              onClose={() => setOpenBilling(false)}
            />
          ) : (
            <Plans
              calledFromAgent={true}
              showPlansModal={showPlansModal}
              setShowPlansModal={setShowPlansModal}
            />
          )}

          {showAddonsModal && (
            <div
              className="close flex-cnt og-close"
              onClick={() => setOpenBilling(false)}
            >
              <svg
                width="10"
                height="10"
                viewBox="0 0 10 10"
                fill="#6D6D6D"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                  fill="#6D6D6D"
                ></path>
              </svg>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

export default PlanAddonModal;

export type AddonModals = "addons" | "downgrade" | "billing";
export type PlanModals = "plan" | "summary" | "payment";
