import { useMutation } from "@tanstack/react-query";
import { axiosPost } from "@/services/axiosService";
import { AxiosError } from "axios";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";

export default function useSaveAgentChat(agentCode: string) {
  const dispatch = useDispatch();

  return useMutation({
    mutationKey: ["agent", agentCode, "chat"],
    mutationFn: async ({
      agentChatId,
      payload,
    }: {
      agentChatId: string | null;
      payload: Record<string, any>;
    }) => {
      const response = await axiosPost(
        `/agent/${agentCode}/chat?agentChatId=${agentChatId ?? ""}`,
        payload,
      );
      const agentChat = response.data.data.agentChat;

      return agentChat;
    },
    onError: (error) => {
      const errMessage =
        error instanceof AxiosError
          ? error.response?.data.message
          : error.message;

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: errMessage || "Something went wrong",
        }),
      );
    },
  });
}
