export default function LeadsPreviewBtn({ title, subTitle, onOpen, aiContentLoader }) {
  return (
    <div className="fx jstfy-end">
      <div className=" linkedin-chat rght-message">
        <div className="inner-bg fx gap-30 pd-10 jstfy-sp-btw ">
          <div className="flex-cnt gap-10">
            <LinkedinLogo />
            <div className="details-chat grid">
              <h6 className="fx al-cnt">{title}</h6>
              <span>{subTitle}</span>
            </div>
          </div>
          <span className={`cp ${aiContentLoader ? "disable" : ""}`} onClick={onOpen}>
          <ArrowImg />
          </span>
        </div>
      </div>
    </div>
  );
}

