import Papa from "papaparse";
import TemplateData from "../audience/template.json";
import { axiosPost } from "@/services/axiosService";
import compData from "../../pages/audience/companies.json";
import empData from "../../pages/audience/employee.json";
import { userSlice } from "@/redux/User";
import { useDispatch } from "react-redux";
import SmallLoader from "@/components/loaders/SmallLoader";
import { useState } from "react";

const CsvInstructions = ({ onUpload, close, uploadType }) => {
  const dispatch = useDispatch();
  const [loader, setLoader] = useState(false);

  async function handleCsvUpload(event) {
    setLoader(true);
    const csvData = await new Promise<Record<string, any>>(
      (resolve, reject) => {
        Papa.parse(event.target.files[0], {
          header: true,
          skipEmptyLines: true,
          transformHeader: (value) => value.trim(),
          complete: (result) => resolve(result),
          error: (error) => reject(error),
        });
      },
    );

    if (uploadType == "company") {
      if (csvData.data[0].hasOwnProperty("Company")) {
        // company_Name
        const data = {
          company: csvData.data,
        };
        const linkedin = await axiosPost(
          `/audience/LinkedinCompanyList?page=0`,
          data,
        );
        onUpload(linkedin.data.data.companyData);
        if (!linkedin.data.data.companyData.length) {
          setLoader(false);
        }
      } else {
        setLoader(false);
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message: "Please provide company_name in your csv list",
          }),
        );
      }
    } else {
      if (
        csvData.data[0].hasOwnProperty("name") &&
        csvData.data[0].hasOwnProperty("email") &&
        csvData.data[0].hasOwnProperty("profileUrn")
      ) {
        onUpload(csvData.data);
      } else {
        setLoader(false);
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message:
              "Please provide name or email or profileUrn in your csv list",
          }),
        );
      }
    }
  }

  function downloadCSV() {
    let datatoparse: any;
    if (uploadType == "company") {
      datatoparse = compData;
    } else if (uploadType == "employee") {
      datatoparse = empData;
    } else {
      datatoparse = TemplateData;
    }
    const csv = Papa.unparse(datatoparse);
    const csvData = new Blob([csv], { type: "text/csv;charset=utf-8" });
    const fileURL = window.URL.createObjectURL(csvData);

    var tempLink = document.createElement("a");
    tempLink.href = fileURL;
    tempLink.setAttribute(
      "download",
      uploadType == "company"
        ? "Omniengage_Companies_Template.csv"
        : "Omniengage_Employees_Template.csv",
    );
    tempLink.click();
  }

  return (
    <>
      {!loader ? (
        <div className="h-100 instructions_wrapper gen-email-modal">
          <div className="call-api-header">
            <h2>
              {uploadType === "company"
                ? "Import Company List"
                : "Import Users List"}
            </h2>
          </div>
          <div className="all-template-body fx fx-d-col pd-20 gap-15">
            <div className="inst_content">
              <h2>
                Lets start with your{" "}
                {uploadType === "company" ? "company" : "user"} data
              </h2>
              <div className="imp_inst">
                <h3>Important instructions</h3>
                <ul>
                  <li>CSV files must have column labels in the first row.</li>
                  <li>CSV files must not contain unicode characters.</li>
                  <li>CSV file must not contain any spaces.</li>
                  <li>
                    Existing {uploadType === "company" ? "companies" : "users"}{" "}
                    that match column labels in your CSV will be updated with
                    new content from your CSV.
                  </li>
                </ul>
              </div>
              <div className="import_mail">
                <div className="import_head">
                  <h3>Import email(s)</h3>
                  <button onClick={downloadCSV}>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="14"
                      height="14"
                      viewBox="0 0 14 14"
                      fill="none"
                    >
                      <g clipPath="url(#clip0_818_66517)">
                        <path
                          d="M6.53621 10.5265C6.59714 10.5875 6.66949 10.6358 6.74911 10.6688C6.82873 10.7018 6.91407 10.7187 7.00026 10.7187C7.08644 10.7187 7.17178 10.7018 7.2514 10.6688C7.33102 10.6358 7.40337 10.5875 7.46431 10.5265L9.93916 8.05167C10.0622 7.9286 10.1314 7.76168 10.1314 7.58763C10.1314 7.41358 10.0622 7.24666 9.93916 7.12359C9.81609 7.00052 9.64917 6.93138 9.47512 6.93138C9.30108 6.93138 9.13416 7.00052 9.01109 7.12359L7.65651 8.47818V1.3125C7.65651 1.13845 7.58737 0.971532 7.46429 0.848461C7.34122 0.72539 7.1743 0.65625 7.00026 0.65625C6.82621 0.65625 6.65929 0.72539 6.53622 0.848461C6.41315 0.971532 6.34401 1.13845 6.34401 1.3125V8.47818L4.98942 7.12359C4.86635 7.00052 4.69943 6.93138 4.52539 6.93138C4.35134 6.93138 4.18442 7.00052 4.06135 7.12359C3.93828 7.24666 3.86914 7.41358 3.86914 7.58763C3.86914 7.76168 3.93828 7.9286 4.06135 8.05167L6.53621 10.5265Z"
                          fill="#6D6D6D"
                        />
                        <path
                          d="M12.6875 6.34375C12.5135 6.34375 12.3465 6.41289 12.2235 6.53596C12.1004 6.65903 12.0312 6.82595 12.0312 7V12.0312H1.96875V7C1.96875 6.82595 1.89961 6.65903 1.77654 6.53596C1.65347 6.41289 1.48655 6.34375 1.3125 6.34375C1.13845 6.34375 0.971532 6.41289 0.848461 6.53596C0.72539 6.65903 0.65625 6.82595 0.65625 7V12.25C0.65625 12.5401 0.771484 12.8183 0.976602 13.0234C1.18172 13.2285 1.45992 13.3438 1.75 13.3438H12.25C12.5401 13.3438 12.8183 13.2285 13.0234 13.0234C13.2285 12.8183 13.3438 12.5401 13.3438 12.25V7C13.3438 6.82595 13.2746 6.65903 13.1515 6.53596C13.0285 6.41289 12.8615 6.34375 12.6875 6.34375Z"
                          fill="#6D6D6D"
                        />
                      </g>
                      <defs>
                        <clipPath id="clip0_818_66517">
                          <rect width="14" height="14" fill="white" />
                        </clipPath>
                      </defs>
                    </svg>
                    Download Sample
                  </button>
                </div>
                <div className="import_table">
                  {uploadType == "company" ? (
                    <table>
                      <thead>
                        <tr>
                          <th>Company*</th>
                          <th>Industry</th>
                          <th>Location</th>
                          <th>Size</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>Apple</td>
                          <td>Retail</td>
                          <td>United States</td>
                          <td>10-100</td>
                        </tr>
                        <tr>
                          <td>Amazon</td>
                          <td>Marketing</td>
                          <td>New York</td>
                          <td>100-500</td>
                        </tr>
                      </tbody>
                    </table>
                  ) : (
                    <table>
                      <thead>
                        <tr>
                          <th>name*</th>
                          <th>profileUrn*</th>
                          <th>email*</th>
                          <th>company</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>John</td>
                          <td>6576853</td>
                          <td>john@gmail.com</td>
                          <td>Amazon</td>
                        </tr>
                        <tr>
                          <td>John</td>
                          <td>6576853</td>
                          <td>john@gmail.com</td>
                          <td>Amazon</td>
                        </tr>
                      </tbody>
                    </table>
                  )}
                </div>
              </div>
            </div>
          </div>
          <div className="call-api-footer">
            <button className="cancel-btn" onClick={close}>
              Cancel
            </button>
            <input
              type="file"
              accept=".csv"
              id="upload-csv"
              hidden
              onChange={handleCsvUpload}
            />
            <label htmlFor="upload-csv" className="save-btn ob-btn-gradient">
              Upload CSV
            </label>
          </div>
        </div>
      ) : (
        <div className="h-100 instructions_wrapper gen-email-modal">
          <div className="call-api-header">
            <h2>Import Company List</h2>
          </div>
          <SmallLoader />
        </div>
      )}
    </>
  );
};

export default CsvInstructions;
