import { useEffect, useState } from "react";
import Smtp from "@/components/integration-modals/Smtp";
import Gmail from "@/components/integration-modals/Gmail";
import "@/styles/config.css";
import "@/styles/builder.css";
import Outlook from "@/components/integration-modals/Outlook";
import SmallLoader from "@/components/loaders/SmallLoader";

function EmailConnect({ onSuccess, onClose }) {
  const [openModalId, setOpenModalId] = useState<ModalId>("");
  const [selectedConfigId, setSelectedConfigId] = useState<ModalId>("");
  const [emailConnecting, setEmailConnecting] = useState(false);

  const handleConfig = (config: ModalId) => {
    if (emailConnecting) return;
    setEmailConnecting(true);
    setOpenModalId(config);
    setSelectedConfigId(config);
  };

  useEffect(() => {
    if (!openModalId) {
      setEmailConnecting(false);
      setSelectedConfigId("");
    }
  }, [openModalId]);

  return (
    <div className="chat-right fx al-cnt gap-15 integration_radios">
      <div
        className={`connect_int p-relative w-mc cp ${selectedConfigId === "gmail" ? "active_int" : ""} ${emailConnecting && selectedConfigId !== "gmail" ? "disable-btn" : ""}`}
        onClick={() => handleConfig("gmail")}
      >
        <div className="connect_int_inner">
          {emailConnecting && selectedConfigId === "gmail" ? (
            <SmallLoader />
          ) : (
            <div className="pseudo-radio"></div>
          )}
          <div className="connect_int_name">
            <GmailIcon />
            <span>Gmail</span>
          </div>
        </div>
      </div>
      <div
        className={`connect_int p-relative w-mc cp ${selectedConfigId === "smtp" ? "active_int" : ""}  ${emailConnecting && selectedConfigId !== "smtp" ? "disable-btn" : ""}`}
        onClick={() => handleConfig("smtp")}
      >
        <div className="connect_int_inner">
          {emailConnecting && selectedConfigId === "smtp" ? (
            <SmallLoader />
          ) : (
            <div className="pseudo-radio"></div>
          )}
          <div className="connect_int_name">
            <SmptIcon />
            <span>SMTP</span>
          </div>
        </div>
      </div>
      <div
        className={`connect_int p-relative w-mc cp ${selectedConfigId === "outlook" ? "active_int" : ""}  ${emailConnecting && selectedConfigId !== "outlook" ? "disable-btn" : ""}`}
        onClick={() => handleConfig("outlook")}
      >
        <div className="connect_int_inner">
          {emailConnecting && selectedConfigId === "outlook" ? (
            <SmallLoader />
          ) : (
            <div className="pseudo-radio"></div>
          )}
          <div className="connect_int_name">
            <OutlookIcon />
            <span>Outlook</span>
          </div>
        </div>
      </div>
      {openModalId === "smtp" && (
        <Smtp
          onSuccess={(config) => {
            setOpenModalId("");
            onSuccess(config);
          }}
          onClose={() => {
            setOpenModalId("");
            onClose();
          }}
        />
      )}
      {openModalId === "gmail" && (
        <Gmail
          onSuccess={(config) => {
            setOpenModalId("");
            onSuccess(config);
          }}
          onClose={() => {
            setOpenModalId("");
            onClose();
          }}
        />
      )}
      {openModalId === "outlook" && (
        <Outlook
          onSuccess={(config) => {
            setOpenModalId("");
            onSuccess(config);
          }}
          onClose={() => {
            setOpenModalId("");
            onClose();
          }}
        />
      )}
    </div>
  );
}

export default EmailConnect;

