import { useEffect, useState } from "react";
import useStore from "../omni-builder/store";

interface Props {
  isGmailPopup: boolean;
  isLinkedInPopup: boolean;
  onConnectLinkedIn: () => void;
  onConnectEmail: () => void;
  onCancel: () => void;
  isliveStatus: number;
}

const ConnectModal: React.FC<Props> = ({
  isLinkedInPopup,
  isliveStatus,
  onConnectEmail,
  onConnectLinkedIn,
  onCancel,
}) => {
  const [isLinkedin, setIsLinkedin] = useState(false);
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  

  // useEffect(() => {
  //   setIsLinkedin(isLinkedInPopup);
  // }, [isLinkedInPopup]);

  return (
    <div
      // className={`createbot-modal leave-bot-modal connect_modal get_credits_modal ${isLinkedin ? "connect_linkedin_modal" : "connect_gmail_modal"}`}
       className={`createbot-modal leave-bot-modal connect_modal get_credits_modal ${isliveStatus === 1 ? "connect_gmail_modal" : isliveStatus == 2 ? "connect_linkedin_modal" : ""}`}
    >
      <div className="createbot-container" onClick={(e) => e.stopPropagation()}>
        <div className="call-api-close close" onClick={onCancel}>
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            />
          </svg>
        </div>
        <div className="success-cont leave-modal">
          <div className="err-header fx-d-col gap-8">
            {/* {isLinkedin ? <LinkedinIcon /> : <GmailSmtpIcon />} */}
            {isliveStatus == 1 ?  <GmailSmtpIcon /> : isliveStatus == 2 ?  <LinkedinIcon />: ""}
            <h1 className="mt-3">
              {isLinkedin ? "Connect Your LinkedIn" : "Connect Your Email"}
            </h1>
          </div>
          <div className="succ-footer">
            <div className="fx">
              {/* {isLinkedin ? (
                <button className="leave-btn mt-0" onClick={onCancel}>
                  Cancel
                </button>
              ) : (
                <button
                  className="leave-btn mt-0"
                  onClick={() => {
                    setOpenConfigModalId("smtp");
                    onCancel();
                  }}
                >
                  Connect SMTP
                </button>
              )} */}
              {isliveStatus == 2 ? (
                <button className="leave-btn mt-0" onClick={onCancel}>
                  Cancel
                </button>
              ) : (
                <button
                  className="leave-btn mt-0"
                  onClick={() => {
                    setOpenConfigModalId("smtp");
                    onCancel();
                  }}
                >
                  Connect SMTP
                </button>
              )}
               <button
                className="stay-btn mt-0"
                onClick={() => {
                  isliveStatus == 2
                    ? onConnectLinkedIn()
                    : isliveStatus == 1 ? setOpenConfigModalId("gmail")  : setOpenConfigModalId(""); 
                  onCancel()
                }}
              >
              {/* <button
                className="stay-btn mt-0"
                onClick={() => {
                  isLinkedin
                    ? onConnectLinkedIn()
                    : setOpenConfigModalId("gmail");
                }}
              > */}
                {/* Connect {isLinkedin ? "LinkedIn" : "Email"} */}
                Connect {isliveStatus == 1 ? "Email" : isliveStatus == 2 ?  "LinkedIn": ""}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

