import { useState } from "react";
import { produce } from "immer";
import { SCHEDULE_NODE, Schedule, NodeType } from "./types";

interface Props {
  scheduleData: Array<Schedule>;
  setScheduleData: (scheduleData: Array<Schedule>) => void;
  generateInitialAIContent: (actualSchedule: any) => void;
  disableGeneration: boolean;
  onProceed: () => void;
  onClose: () => void;
}

export default function ScheduleWindow({
  scheduleData,
  setScheduleData,
  generateInitialAIContent,
  disableGeneration = false,
  onProceed,
  onClose,
}: Props) {
  const [accordionIndex, setAccordionIndex] = useState<number | null>(null);
  const [openDropdownIndex, setOpenDropdownIndex] = useState<number | null>(
    null,
  );

  function addDay() {
    const maxDay = scheduleData.reduce(
      (pre, data) => Math.max(pre, data.day),
      0,
    );

    const _scheduleData: Array<Schedule> = [
      ...scheduleData,
      {
        day: maxDay + 1,
        nodes: ["email"],
      },
    ];

    setScheduleData(_scheduleData);
  }

  function deleteDay(index: number) {
    const _scheduleData = produce(scheduleData, (draft) => {
      draft.splice(index, 1);
    });
    setScheduleData(_scheduleData);
  }

  function toggleAccordion(index: number) {
    if (accordionIndex === index) setAccordionIndex(null);
    else setAccordionIndex(index);
  }

  function handleDateChange(index: number, value: number) {
    const _scheduleData = produce(scheduleData, (draft) => {
      draft[index].day = value;
    });
    setScheduleData(_scheduleData);
  }

  function toggleNode(index: number, node: NodeType) {
    const _scheduleData = produce(scheduleData, (draft) => {
      const nodeIndex = draft[index].nodes.findIndex((_node) => _node === node);
      if (nodeIndex === -1) draft[index].nodes.push(node);
      else draft[index].nodes.splice(nodeIndex, 1);

      if (!draft[index].nodes.length) draft.splice(index, 1);
    });
    setScheduleData(_scheduleData);
  }

  function saveAndGenInitialAIContent() {
    if (
      disableGeneration ||
      scheduleData.length === 0 ||
      scheduleData.some((sd) => sd.nodes.length === 0)
    )
      return;
    const actualSchedule = combineAndSortDays();
    generateInitialAIContent(actualSchedule);
    onProceed();
    onClose();
  }

  function combineAndSortDays() {
    let deepSD = JSON.parse(JSON.stringify(scheduleData));
    deepSD = deepSD.filter((sd: Schedule) => sd.nodes.length > 0);
    const _scheduleData: Schedule[] = deepSD.reduce((acc: any[], curr: any) => {
      const index = acc.findIndex((sd: Schedule) => sd.day === curr.day);
      if (index === -1) {
        acc.push(curr);
      } else {
        curr.nodes.forEach((node: NodeType) => {
          if (!acc[index].nodes.includes(node)) {
            acc[index].nodes.push(node);
          }
        });
      }
      return acc;
    }, []);

    _scheduleData.sort((a, b) => a.day - b.day);
    setScheduleData(_scheduleData);
    return _scheduleData;
  }

  return (
    <div className="gen-email-modal ob-table-sidebar pd-20 edit_details_wrapper">
      <div className="h-100 fx fx-d-col gap-10">
        <div className="all-template-body pd-20 ovr-flw-y-au">
          <div className="import_mail fx fx-d-col pd-20 gap-20">
            <div className="import_head">
              <h3>Campaign Overview</h3>
              <p>
                Preview your campaign timeline and planned outreach channels.
              </p>
            </div>
            <div className="user_details_container">
              <div className="fx user_details_label">
                <p>Day Selection</p>
                <p>Channel Selection</p>
              </div>
              <div className="user_details_wrapper mt-10 grid gap-8">
                {scheduleData.map((data, scheduleIndex) => (
                  <div
                    key={scheduleIndex}
                    className={`user_detail_box fx al-cnt ${accordionIndex === scheduleIndex ? "edit_data" : ""}`}
                  >
                    {accordionIndex === scheduleIndex ? (
                      <>
                        <div className="day fx fx-d-col al-fx-start gap-5">
                          <h4>Add</h4>
                          <div className="add_input fx al-cnt gap-10">
                            <label>Day:</label>
                            <input
                              type="number"
                              value={data.day}
                              onChange={(event) =>
                                handleDateChange(
                                  scheduleIndex,
                                  Number(event.target.value),
                                )
                              }
                            />
                          </div>
                        </div>
                        <div className="fx fx-d-col publish_input_wrapper publish_input wd-100 al-fx-start gap-5">
                          <h4>Select Data:</h4>
                          <div className="add_input pd-10 fx al-cnt fx-wrap gap-5">
                            {Object.entries(SCHEDULE_NODE).map(([id, name]) => (
                              <div
                                key={id}
                                className="to-assign fx al-cnt gap-10"
                              >
                                <label
                                  className={`checkbox-container assign-logic fx ${data.nodes.includes(id as NodeType) ? "checked" : ""}`}
                                >
                                  <input
                                    type="checkbox"
                                    id="main_box"
                                    checked={data.nodes.includes(id as any)}
                                    onClick={() =>
                                      toggleNode(scheduleIndex, id as NodeType)
                                    }
                                  />
                                  <div className="mark">
                                    <CheckMark />
                                  </div>
                                  <span>{name}</span>
                                </label>
                              </div>
                            ))}
                          </div>
                          <button
                            className="done_btn"
                            onClick={() => toggleAccordion(scheduleIndex)}
                          >
                            Done
                          </button>
                        </div>
                      </>
                    ) : (
                      <>
                        <div className="day fs-0">Day {data.day}</div>
                        <div className="fx fx-wrap publish_input_wrapper publish_input wd-100 al-cnt p-relative gap-5">
                          {data.nodes.map((node) => (
                            <div
                              key={node}
                              className="fx al-cnt w-mc trigger_msg gap-8"
                            >
                              <span>{SCHEDULE_NODE[node]}</span>
                              <span
                                className="cp grid"
                                onClick={() => toggleNode(scheduleIndex, node)}
                              >
                                <CrossIconSvg />
                              </span>
                            </div>
                          ))}
                          <span
                            // onClick={() =>
                            //   setDropdownId(dropdownId ? 0 : data.day)
                            // }
                            // onMouseLeave={() => setDropdownId(0)}
                            onClick={() =>
                              setOpenDropdownIndex(
                                openDropdownIndex === scheduleIndex
                                  ? null
                                  : scheduleIndex,
                              )
                            }
                            onMouseLeave={() => setOpenDropdownIndex(null)}
                            className="p-absolute right-12"
                          >
                            <ThreeDots />
                            {openDropdownIndex === scheduleIndex && (
                              <div className="dropdown-items w-100 full-view-menu new-scrollbar">
                                <ul className="grey-scrollbar fx fx-d-col gap-0">
                                  <li
                                    className="fx al-cnt mb-0 gap-10"
                                    onClick={() =>
                                      toggleAccordion(scheduleIndex)
                                    }
                                  >
                                    <svg
                                      width={16}
                                      height={17}
                                      viewBox="0 0 16 17"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        fillRule="evenodd"
                                        clipRule="evenodd"
                                        d="M14.5712 1.92946C13.5208 0.879125 12.4634 0.410745 11.3969 0.515436C10.368 0.616428 9.51533 1.23692 8.8228 1.92946L1.27412 9.47814C1.05984 9.69242 0.875084 9.97606 0.737866 10.2491C0.600941 10.5215 0.481658 10.8423 0.438509 11.1501L0.438327 11.1514L0.0271303 14.0298L0.026919 14.0312C-0.0776349 14.7462 0.117976 15.4266 0.596198 15.9029C1.07339 16.3781 1.75357 16.5722 2.46445 16.4744L2.46445 16.4744L2.46811 16.4739L5.35155 16.062C5.65528 16.0186 5.97388 15.8987 6.24436 15.7627C6.51644 15.626 6.79835 15.4435 7.01408 15.2348L7.01415 15.2348L7.02248 15.2265L13.4198 8.82915C13.4699 8.79085 13.5151 8.74586 13.554 8.69501L14.5712 7.67781C15.2637 6.98528 15.8842 6.13258 15.9852 5.10373C16.0899 4.03719 15.6215 2.97979 14.5712 1.92946ZM12.7777 7.4391L13.5551 6.66175C14.1654 6.05147 14.5028 5.49597 14.5551 4.96336C14.6037 4.46843 14.4207 3.8111 13.5551 2.94552C12.6895 2.07994 12.0322 1.89691 11.5373 1.94549C11.0046 1.99778 10.4491 2.33524 9.83886 2.94552L9.06483 3.71955C9.67665 5.44976 11.0397 6.8164 12.7777 7.4391ZM7.96238 4.822L2.29018 10.4942C2.21707 10.5673 2.11444 10.71 2.02175 10.8944C1.92896 11.079 1.87595 11.2462 1.86147 11.35L1.86114 11.3523L1.44922 14.2357L1.44883 14.2385C1.40058 14.5671 1.49721 14.7722 1.6102 14.8848C1.72435 14.9985 1.93458 15.0964 2.26693 15.0511L2.26855 15.0509L5.14834 14.6395C5.24694 14.6254 5.41212 14.5728 5.59906 14.4789C5.78179 14.387 5.92935 14.2837 6.01132 14.2055L11.6768 8.54009C10.0328 7.783 8.71371 6.46336 7.96238 4.822Z"
                                        fill="#444444"
                                      />
                                    </svg>
                                    Edit
                                  </li>
                                  <li
                                    className="fx al-cnt gap-10"
                                    onClick={() => deleteDay(scheduleIndex)}
                                  >
                                    <svg
                                      width={14}
                                      height={17}
                                      viewBox="0 0 14 17"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        d="M13.0002 3.16667H10.9335C10.7788 2.41428 10.3694 1.73823 9.77433 1.25248C9.17928 0.766727 8.43497 0.50097 7.66683 0.5L6.3335 0.5C5.56536 0.50097 4.82104 0.766727 4.226 1.25248C3.63095 1.73823 3.22156 2.41428 3.06683 3.16667H1.00016C0.823352 3.16667 0.653782 3.2369 0.528758 3.36193C0.403734 3.48695 0.333496 3.65652 0.333496 3.83333C0.333496 4.01014 0.403734 4.17971 0.528758 4.30474C0.653782 4.42976 0.823352 4.5 1.00016 4.5H1.66683V13.1667C1.66789 14.0504 2.01942 14.8976 2.64431 15.5225C3.2692 16.1474 4.11643 16.4989 5.00016 16.5H9.00016C9.88389 16.4989 10.7311 16.1474 11.356 15.5225C11.9809 14.8976 12.3324 14.0504 12.3335 13.1667V4.5H13.0002C13.177 4.5 13.3465 4.42976 13.4716 4.30474C13.5966 4.17971 13.6668 4.01014 13.6668 3.83333C13.6668 3.65652 13.5966 3.48695 13.4716 3.36193C13.3465 3.2369 13.177 3.16667 13.0002 3.16667ZM6.3335 1.83333H7.66683C8.08035 1.83384 8.48358 1.96225 8.82124 2.20096C9.1589 2.43967 9.41444 2.77699 9.55283 3.16667H4.4475C4.58588 2.77699 4.84143 2.43967 5.17909 2.20096C5.51675 1.96225 5.91998 1.83384 6.3335 1.83333ZM11.0002 13.1667C11.0002 13.6971 10.7894 14.2058 10.4144 14.5809C10.0393 14.956 9.5306 15.1667 9.00016 15.1667H5.00016C4.46973 15.1667 3.96102 14.956 3.58595 14.5809C3.21088 14.2058 3.00016 13.6971 3.00016 13.1667V4.5H11.0002V13.1667Z"
                                        fill="#444444"
                                      />
                                      <path
                                        d="M5.66667 12.5002C5.84348 12.5002 6.01305 12.43 6.13807 12.3049C6.2631 12.1799 6.33333 12.0103 6.33333 11.8335V7.83354C6.33333 7.65673 6.2631 7.48716 6.13807 7.36213C6.01305 7.23711 5.84348 7.16687 5.66667 7.16687C5.48986 7.16687 5.32029 7.23711 5.19526 7.36213C5.07024 7.48716 5 7.65673 5 7.83354V11.8335C5 12.0103 5.07024 12.1799 5.19526 12.3049C5.32029 12.43 5.48986 12.5002 5.66667 12.5002ZM8.33333 12.5002C8.51014 12.5002 8.67971 12.43 8.80474 12.3049C8.92976 12.1799 9 12.0103 9 11.8335V7.83354C9 7.65673 8.92976 7.48716 8.80474 7.36213C8.67971 7.23711 8.51014 7.16687 8.33333 7.16687C8.15652 7.16687 7.98695 7.23711 7.86193 7.36213C7.7369 7.48716 7.66667 7.65673 7.66667 7.83354V11.8335C7.66667 12.0103 7.7369 12.1799 7.86193 12.3049C7.98695 12.43 8.15652 12.5002 8.33333 12.5002Z"
                                        fill="#444444"
                                      />
                                    </svg>
                                    Delete
                                  </li>
                                </ul>
                              </div>
                            )}
                          </span>
                        </div>
                      </>
                    )}
                  </div>
                ))}
              </div>
            </div>
          </div>
          <span className="fx al-cnt add_day mt-10 gap-5" onClick={addDay}>
            <PLusIcon /> Add new Day
          </span>
        </div>
        <div className="call-api-footer fx fx-d-row">
          {/* <button className="no_bg_btn">
            <span>Edit Sequence</span>
          </button> */}
          {
            <>
              <button className="cancel-btn cp" onClick={onClose}>
                Close
              </button>
              <button
                className="btn_wth_bg btn-agent"
                onClick={() => saveAndGenInitialAIContent()}
                disabled={
                  disableGeneration ||
                  scheduleData.length === 0 ||
                  scheduleData.some((sd) => sd.nodes.length === 0)
                }
              >
                <span>Proceed</span>
              </button>
            </>
          }
        </div>
      </div>
    </div>
  );
}

const CrossIconSvg = () => (
  <svg
    width={18}
    height={19}
    viewBox="0 0 18 19"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <rect y={0.5} width={18} height={18} rx={9} fill="white" />
    <path
      d="M13 6.30571L12.1943 5.5L9 8.69429L5.80571 5.5L5 6.30571L8.19429 9.5L5 12.6943L5.80571 13.5L9 10.3057L12.1943 13.5L13 12.6943L9.80571 9.5L13 6.30571Z"
      fill="#FB5F66"
    />
  </svg>
);

const ThreeDots = () => (
  <svg
    width={16}
    height={17}
    viewBox="0 0 16 17"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
    className="cp"
  >
    <g clipPath="url(#clip0_1087_13803)">
      <path
        d="M8.00004 9.16683C8.36823 9.16683 8.66671 8.86835 8.66671 8.50016C8.66671 8.13197 8.36823 7.8335 8.00004 7.8335C7.63185 7.8335 7.33337 8.13197 7.33337 8.50016C7.33337 8.86835 7.63185 9.16683 8.00004 9.16683Z"
        stroke="#444444"
        strokeWidth={2}
        strokeLinecap="round"
        strokeLinejoin="round"
      />
      <path
        d="M8.00004 13.8333C8.36823 13.8333 8.66671 13.5349 8.66671 13.1667C8.66671 12.7985 8.36823 12.5 8.00004 12.5C7.63185 12.5 7.33337 12.7985 7.33337 13.1667C7.33337 13.5349 7.63185 13.8333 8.00004 13.8333Z"
        stroke="#444444"
        strokeWidth={2}
        strokeLinecap="round"
        strokeLinejoin="round"
      />
      <path
        d="M8.00004 4.49984C8.36823 4.49984 8.66671 4.20136 8.66671 3.83317C8.66671 3.46498 8.36823 3.1665 8.00004 3.1665C7.63185 3.1665 7.33337 3.46498 7.33337 3.83317C7.33337 4.20136 7.63185 4.49984 8.00004 4.49984Z"
        stroke="#444444"
        strokeWidth={2}
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </g>
    <defs>
      <clipPath id="clip0_1087_13803">
        <rect
          width={16}
          height={16}
          fill="white"
          transform="translate(0 0.5)"
        />
      </clipPath>
    </defs>
  </svg>
);

const PLusIcon = () => (
  <svg
    width={18}
    height={18}
    viewBox="0 0 18 18"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M9 16.5C13.125 16.5 16.5 13.125 16.5 9C16.5 4.875 13.125 1.5 9 1.5C4.875 1.5 1.5 4.875 1.5 9C1.5 13.125 4.875 16.5 9 16.5Z"
      stroke="#FB5F66"
      strokeWidth={1.125}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
    <path
      d="M6 9H12"
      stroke="#FB5F66"
      strokeWidth={1.13}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
    <path
      d="M9 12V6"
      stroke="#FB5F66"
      strokeWidth={1.13}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);

function CheckMark() {
  return (
    <svg
      width="10"
      height="8"
      viewBox="0 0 10 8"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M3.22883 7.60948C2.94497 7.60959 2.67274 7.49677 2.47219 7.29588L0.184609 5.00915C-0.0615362 4.76293 -0.0615362 4.3638 0.184609 4.11758C0.430832 3.87143 0.829961 3.87143 1.07618 4.11758L3.22883 6.27022L8.92382 0.575234C9.17004 0.329089 9.56917 0.329089 9.81539 0.575234C10.0615 0.821457 10.0615 1.22059 9.81539 1.46681L3.98547 7.29588C3.78492 7.49677 3.51269 7.60959 3.22883 7.60948Z"
        fill="white"
      />
    </svg>
  );
}
