import { useState } from "react";

export default function UpdateOptions({ onProceed }) {
  const [selectedOption, setSelectedOption] = useState<any>(null);

  return (
    <div className="fx al-cnt gap-15 update_options_wrapper fx-wrap">
      {OPTIONS.map((option) => (
        <div
          className={`connect_int p-relative w-mc cp ${selectedOption === option.id ? "active_int" : ""}`}
          onClick={() => {
            setSelectedOption(option.id);
            onProceed(option.sourceHandle, option.name);
          }}
        >
          <div className="connect_int_inner" key={option.name}>
            <div className="pseudo-radio"></div>
            <div className="connect_int_name gap-5">
              {option.icon}
              <span>{option.name}</span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

