import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { userSlice } from "@/redux/User";

function AgentToken({ trial, creditInSufficient, handleNavigation }) {
  const router = useNavigate();
  const dispatch = useDispatch();

  return (
    <div className="token-bar fx al-cnt jstfy-sp-btw">
      <div className="token-bar_lft flex-cnt gap-10">
        <ExclamatrySVG />
        {creditInSufficient ? (
          <p>
            Your credits are insufficient for this request!
            <span className="why_credit p-relative">
              {" "}
              Why?
              <div className="tooltip_content">
                <ul>
                  <li>Try modifying your schedule.</li>
                  <li>Reduce number of selected leads.</li>
                </ul>
              </div>
            </span>
          </p>
        ) : trial ? (
          <p>
            Your credit <b>limit has exhausted!</b>
            <span> Buy our plan</span> now at 22$/month.
          </p>
        ) : (
          <p>
            Your credit <b>limit has exhausted!</b>
            <span> Add more credits</span> to your existing plan.
          </p>
        )}
      </div>
      <button onClick={handleNavigation}>
        {trial ? "Get credits" : "Add More Credits"}
      </button>
    </div>
  );
}

export default AgentToken;

