import type { Node, Edge, Connection } from "@xyflow/react";
import { Schedule } from "./types";
import getEdgeId from "../omni-builder/utils/getEdgeId";

export default function createWorkflow(
  scheduleData: Array<Schedule>,
  audienceData: Record<string, any>,
  emailConfig: Record<string, any> | null,
  linkedInConfig: Record<string, any> | null,
) {
  let nodeId = 1;
  const nodes: Array<Node<Record<string, any>>> = [
    {
      id: String(nodeId++),
      type: "campaign",
      position: { x: 100, y: nodeId * 200 },
      data: {
        nodeType: "actions",
        audienceData: {
          name: audienceData.name,
          type: "uploadList",
          listId: audienceData.listId,
          segmentId: "",
          scheduleType: "now",
          scheduleTime: "",
        },
      },
    },
  ];
  const edges: Array<Edge> = [];
  const createConnection = () => {
    const connection: Connection = {
      source: String(nodeId - 1),
      target: String(nodeId),
      sourceHandle: "output_1",
      targetHandle: "input_1",
    };
    const id = getEdgeId(connection);
    edges.push({ id, ...connection });
  };

  const _scheduleData = structuredClone(scheduleData);
  _scheduleData.sort((a, b) => a.day - b.day);

  for (let index = 0; index < _scheduleData.length; index++) {
    const data = scheduleData[index];
    const delay = data.day - (scheduleData[index - 1]?.day ?? 1);

    if (delay) {
      createConnection();
      nodes.push({
        id: String(nodeId++),
        type: "delayAction",
        position: { x: 100, y: nodeId * 200 },
        data: {
          nodeType: "actions",
          delayAction: {
            time: delay,
            timeFormat: "days",
          },
        },
      });
    }

    for (const nodeType of data.nodes) {
      createConnection();
      const common: Omit<Node, "data"> = {
        id: String(nodeId++),
        position: { x: 100, y: nodeId * 200 },
      };

      switch (nodeType) {
        case "linkedinVisit": {
          nodes.push({
            ...common,
            type: "linkedIn",
            data: {
              nodeType: "actions",
              linkedInData: {
                nodeType: "visit",
                configId: linkedInConfig?._id || "",
                name: "{ name }",
                email: "{ email }",
                profileId: "{ profileUrn }",
                message: "",
              },
            },
          });
          break;
        }
        case "linkedinConnect": {
          nodes.push({
            ...common,
            type: "linkedIn",
            data: {
              nodeType: "actions",
              linkedInData: {
                nodeType: "connect",
                configId: linkedInConfig?._id || "",
                name: "{ name }",
                email: "{ email }",
                profileId: "{ profileUrn }",
                message: "",
              },
            },
          });
          break;
        }
        case "linkedinMsg": {
          nodes.push({
            ...common,
            type: "linkedIn",
            data: {
              nodeType: "actions",
              linkedInData: {
                nodeType: "message",
                configId: linkedInConfig?._id || "",
                name: "{ name }",
                email: "{ email }",
                profileId: "{ profileUrn }",
                message: `{ day_${data.day}_linkedinMsg }`,
              },
            },
          });
          break;
        }
        // case "makeConditionalDecision": {
        //   nodes.push({
        //     ...common,
        //     data: {
        //       conditionData: [
        //         {
        //           variable: node.data?.firstOperand || "",
        //           value: node.data?.secondOperand || "",
        //           comparison_operator: node.data?.operator || "EQUAL TO",
        //           logical_operator: "AND",
        //         },
        //       ],
        //     },
        //   });
        //   break;
        // }
        case "email":
        default: {
          nodes.push({
            ...common,
            type: "sendEmail",
            data: {
              type: "sendEmail",
              nodeType: "actions",

              sendEmailDetails: {
                from: emailConfig?.sender || "",
                to: "{ email }",
                content: {
                  subject: `{ day_${data.day}_emailSubject }`,
                  body: `{ day_${data.day}_emailBody }`,
                },
                emailConfig: emailConfig?._id || "",
              },
            },
          });
          break;
        }
      }
    }
  }

  return { nodes, edges };
}
