import { motion } from "framer-motion";
interface Props {
  suggestions: Array<string>;
  first: boolean;
  onClick: (suggestion: string) => void;
}

function AssistantSuggestions({ suggestions, first, onClick }: Props) {
  return (
    <div
      className={`asst_suggest_wrapper grid ${first ? "gap-20" : "asst_suggest_wrapper2 gap-10"}`}
    >
      <div className="suggest-i grid-cnt grid gap-10">
        {first && <SuggestionIcon />}
        <h2>Suggestions</h2>
      </div>
      <div className="suggest_reply_wrapper fx">
        {suggestions.map((suggestion, i) => (
          <motion.div
            key={i}
            initial={{ opacity: 0, scale: 0.5 }}
            animate={{ opacity: 1, scale: 1 }}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.85 }}
            className="suggest_reply flex-cnt cursor-pointer"
            onClick={() => onClick(suggestion)}
          >
            <p>{suggestion}</p>
            {/* <span className={`flex-cnt fs-0 cp ${first ? "" : "none"}`}>
              <UpArrow />
            </span> */}
          </motion.div>
        ))}
      </div>
    </div>
  );
}

export default AssistantSuggestions;

