import { useEffect, useRef, useState } from "react";
import VariableDropdown from "../omni-builder/components/VariableDropdown";
import { Variable } from "../omni-builder/types";
import placeVariable from "../omni-builder/utils/placeVariable";
import Picker from "@emoji-mart/react";
import emojiData from "@emoji-mart/data";
import { useDispatch } from "react-redux";
import { useSearchParams } from "react-router-dom";
import Froala from "../omni-builder/components/Froala";
import { JSONTree } from "react-json-tree";
import "react-quill/dist/quill.snow.css";
import "@/styles/quill.css";
import Editor from "@/components/shared/QuillEditor/Editor";

interface EmailBodyModalProps {
  setEditEmailTable: React.Dispatch<React.SetStateAction<boolean>>;
  setShowGenWithAiModal: React.Dispatch<React.SetStateAction<boolean>>;
  setSubject: React.Dispatch<React.SetStateAction<string>>;
  subject: string;
  onSave: any;
  body: string;
  setBody: React.Dispatch<React.SetStateAction<any>>;
  generateContent: (body, subject) => void;
  allData: any;
  messageType: string;
  loaders?: boolean;
  setloaders?: any;
}

const EmailBodyModal = ({
  setEditEmailTable,
  setShowGenWithAiModal,
  setSubject,
  subject,
  onSave,
  body,
  setBody,
  generateContent,
  allData,
  messageType,
  loaders,
  setloaders,
}: EmailBodyModalProps) => {
  const debug = useRef<boolean>(false);
  const [searchParams, _] = useSearchParams();
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const fieldInputRefs = useRef<FieldInputRefs>({});
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [bodyVar, setBodyVar] = useState<Variable>();

  console.log(loaders, "loaders are");

  const handleChange = (value: string) => {
    if (value.trim() === "" || /^\S+$/.test(value.trim())) {
      setBody(body);
    } else {
      setBody(value);
    }
  };

  const handleSave = async () => {
    const bodyToProcess = body || "";

    const processContent = (content) => {
      const froalaPhrases = [
        "Powered by Froala Editor",
        "Froala EditorPowered by Froala Editor",
        "Froala Editor",
        "Powered by",
        "Froala",
      ];
      froalaPhrases.forEach((phrase) => {
        content = content.replaceAll(phrase, "");
      });

      const parser = new DOMParser();
      const doc = parser.parseFromString(content, "text/html");
      let processedText = "";
      doc.body.querySelectorAll("p").forEach((p) => {
        processedText += p.textContent + "\n";
      });

      return processedText.trim();
    };

    let processedBody = bodyToProcess;

    processedBody = processContent(processedBody);

    onSave(subject, processedBody, body);
    setEditEmailTable(false);
  };

  useEffect(() => {
    debug.current = searchParams.get("debug") === "true" ? true : false;
  }, []);
  const dispatch = useDispatch();

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  // useEffect(() => {
  //   generateContent(body, subject);
  // }, []);

  function handleVariableSelect(field: Field, variable: Variable) {
    if (field === "content.body") {
      setBody((prevValue) => placeVariable(prevValue, variable));
      return;
    }
    const inputRef = fieldInputRefs.current[field];
    if (!inputRef) return;

    const result = placeVariable(inputRef, variable);
    // onDataChange(`sendEmailDetails.${field}`, result);
  }
  return (
    <div className="createbot-modal createbot-new-background modal-scroll whatsapp-template-modal ob-table-modal email_body_modal">
      <div className="call-api-container mh-85 w-680 email-modal-height">
        <div
          className="call-api-close close"
          onClick={() => setEditEmailTable(false)}
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            ></path>
          </svg>
        </div>
        <div className="call-api-header">
          <h2>{messageType == "email" ? "Email" : "Message"} Content</h2>
          <p>
            Edit your {messageType == "email" ? "email" : "message"} body
            content or you can generate from AI
          </p>
        </div>
        <div className="call-api-main no-m">
          <div className="call-api-inner pd-30">
            <div>
              <span className="data-box-label fx jstfy-sp-btw">
                <span className="email_content">
                  {messageType == "email" ? "Email" : "Message"} Contents
                </span>
                <button
                  className="generate-btn fx al-cnt"
                  onClick={() => {
                    generateContent(body, subject);
                    setloaders(true);
                  }}
                >
                  <svg
                    width="14"
                    height="14"
                    viewBox="0 0 14 14"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M12.25 3.50001C12.25 2.53348 13.0335 1.75 14 1.75C13.0335 1.75 12.25 0.966502 12.25 0C12.25 0.966473 11.4665 1.75 10.5 1.75C11.4665 1.75 12.25 2.53345 12.25 3.50001ZM12.6875 5.24998C12.6875 5.97484 12.0999 6.56248 11.375 6.56248C12.0999 6.56248 12.6875 7.15011 12.6875 7.87497C12.6875 7.15008 13.2751 6.56248 14 6.56248C13.2751 6.56251 12.6875 5.97487 12.6875 5.24998ZM6.125 5.24998C6.125 3.80023 7.30024 2.62499 8.74999 2.62499C7.30024 2.62499 6.125 1.44975 6.125 0C6.125 1.44975 4.94976 2.62499 3.50001 2.62499C4.94976 2.62499 6.125 3.80026 6.125 5.24998ZM9.62501 2.62499L0 12.25L1.75 14L11.375 4.37499L9.62501 2.62499ZM8.13134 5.43115L9.36863 4.19386L9.80614 4.63137L8.56885 5.86866L8.13134 5.43115Z"
                      fill="#FB5F66"
                    />
                  </svg>
                  {loaders ? "Generating..." : "Regenerate With AI"}
                </button>
              </span>
              <div className="data-box-wrap">
                {messageType == "email" && (
                  <>
                    <div className="row-container">
                      <span className="input-label">Subject</span>
                      <div
                        className="node-dropdown h-mc"
                        onClick={() => handleDropdown("content.subject")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        {/* <span className="color-red">Add Variable</span>
                        {openDropdownId === "content.subject" && (
                          <VariableDropdown
                            onSelect={(variable) =>
                              handleVariableSelect("content.subject", variable)
                            }
                          />
                        )} */}
                      </div>
                    </div>
                    <div className="rightSidebar-tarea fx emoji_input">
                      <input
                        className="rightSidebar-tarea no-m bd-none no-bg"
                        ref={(ref) =>
                          ref &&
                          (fieldInputRefs.current["content.subject"] = ref)
                        }
                        value={subject}
                        placeholder="Write Something Here....."
                        onChange={(e) => setSubject(e.target.value)}
                      />
                      <button
                        className="p-relative no-bg bd-none"
                        onClick={() => setShowEmojiPicker(!showEmojiPicker)}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 16 16"
                          fill="none"
                        >
                          <path
                            d="M7.99563 1.88867C4.62229 1.88867 1.89062 4.62645 1.89062 7.99978C1.89062 11.3731 4.62229 14.1109 7.99563 14.1109C11.3751 14.1109 14.1128 11.3731 14.1128 7.99978C14.1128 4.62645 11.3751 1.88867 7.99563 1.88867ZM8.00174 12.8887C5.30063 12.8887 3.11285 10.7009 3.11285 7.99978C3.11285 5.29867 5.30063 3.11089 8.00174 3.11089C10.7028 3.11089 12.8906 5.29867 12.8906 7.99978C12.8906 10.7009 10.7028 12.8887 8.00174 12.8887ZM10.1406 7.38867C10.6478 7.38867 11.0573 6.97923 11.0573 6.47201C11.0573 5.96478 10.6478 5.55534 10.1406 5.55534C9.6334 5.55534 9.22396 5.96478 9.22396 6.47201C9.22396 6.97923 9.6334 7.38867 10.1406 7.38867ZM5.86285 7.38867C6.37007 7.38867 6.77951 6.97923 6.77951 6.47201C6.77951 5.96478 6.37007 5.55534 5.86285 5.55534C5.35563 5.55534 4.94618 5.96478 4.94618 6.47201C4.94618 6.97923 5.35563 7.38867 5.86285 7.38867ZM8.00174 11.3609C9.42563 11.3609 10.6356 10.4687 11.1245 9.22201H4.87896C5.36785 10.4687 6.57785 11.3609 8.00174 11.3609Z"
                            fill="black"
                            fillOpacity="0.54"
                          />
                        </svg>
                      </button>
                      <div className="p-absolute emoji-picker">
                        {showEmojiPicker && (
                          <Picker
                            data={emojiData}
                            previewPosition="none"
                            emojiSize="18"
                            maxFrequentRows="1"
                            onEmojiSelect={(e) =>
                              setSubject((prevValue) => prevValue + e.native)
                            }
                            onClickOutside={() => setShowEmojiPicker(false)}
                          />
                        )}
                      </div>
                    </div>{" "}
                  </>
                )}

                <div className="row-container mg-top">
                  <span className="input-label webhook_head">Body</span>
                  <div
                    className="node-dropdown h-mc"
                    onClick={() => handleDropdown("content.body")}
                    onMouseLeave={() => handleDropdown("")}
                  >
                    {/* <span className="color-red">Add Variable</span>
                    {openDropdownId === "content.body" && (
                      <VariableDropdown
                        onSelect={(variable) =>
                          handleVariableSelect("content.body", variable)
                        }
                      />
                    )} */}
                  </div>
                </div>
                {/* <Froala
               type="sticky"
               content={
                 body ? body.replace("Powered by Froala Editor", "") : body
                }
                onContentChange={(content) => {
                if (content.trim() === "" || /^\s*\S+\s*$/.test(content)) {
                setBody(body);
                } else {
                setBody(content);
                }
  }}
/> */}
                <div className="editor-container no-m bd-none">
                  <Editor
                    value={body.replaceAll("\n", "<br>")}
                    onChange={handleChange}
                    showVariables={false}
                  />
                </div>

                {debug.current && (
                  <>
                    {" "}
                    <div className="row-container mg-top">
                      <span className="input-label webhook_head">
                        Debug Data
                      </span>
                    </div>
                    <JSONTree
                      hideRoot
                      invertTheme={true}
                      theme="greenscreen"
                      data={allData?.debug_data}
                    />
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
        <div className="call-api-footer p-relative">
          {/* <button className="save-btn">Apply Changes</button> */}
          <button
            className="save-btn w-mc ob-btn-gradient"
            onClick={handleSave}
          >
            Apply Changes
          </button>
        </div>
      </div>
    </div>
  );
};

export default EmailBodyModal;

type Field = "from" | "to" | "replyTo" | "content.subject" | "content.body";
type DropDownId = Field | "config" | "";
type FieldInputRefs = Partial<Record<Field, React.ElementRef<"input">>>;
