import { motion } from "framer-motion";

interface Props {
  children: React.ReactNode;
  onClick: () => void;
  selectedOption?: any;
  type?: any;
  isSkip?:boolean;
}

export default function Button({ children, onClick, selectedOption, type, isSkip = false }) {
  return (
    <motion.div
    whileHover={{ scale: 1.05 }}
    whileTap={{ scale: 0.85 }}
      className={`connect_int p-relative w-mc cp csv_upload_int agent_btn ${type === selectedOption ? "active_int" : ""} ${isSkip ? "skip_btn" : ""}`}
      onClick={onClick} >
      <div className="connect_int_inner">
      <div className="pseudo-radio"></div>
        <div className="connect_int_name">{children}</div>
      </div>
    </motion.div>
  );
}
