import { SCHEDULE_NODE } from "./types";

export default function Sequence({ scheduleData, onOpen, aiContentLoader }) {
  return (
    <div className="sequence_wrapper">
      <table className="fx">
        <thead>
          <tr className="fx p-relative">
            <th>DAY</th>
            <th>SEQUENCE</th>
            <span className={`p-absolute cp ${aiContentLoader ? "disable" : ""}`} onClick={onOpen}>
              <ShareIcon />
            </span>
          </tr>
        </thead>
        <tbody>
          {scheduleData.map((data, index) => (
            <tr key={index} className="fx">
              <td>Day {data.day}</td>
              <td>
                <ul className="fx gap-8">
                  {data.nodes.map((node, nodeIndex) => (
                    <li key={nodeIndex}>{SCHEDULE_NODE[node]}</li>
                  ))}
                </ul>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

